// Utility: simple debounce function
function debounce<T extends (...args: any[]) => any>(fn: T, delay: number): T {
    let timeout: NodeJS.Timeout | null = null;
    return ((...args: any[]) => {
        if (timeout) {
            clearTimeout(timeout);
        }
        return new Promise((resolve) => {
            timeout = setTimeout(async () => {
                resolve(await fn(...args));
            }, delay);
        });
    }) as T;
}


/**
 * AICodeCompletionProvider now includes:
 * - A 1-second debounce delay
 * - Checks for meaningful trigger patterns and minimal changes (95% similarity threshold)
 * - A 30-second cache keyed by file+cursor+context
 * - Aborting in-flight API requests if new ones are triggered
 */
export class AICodeCompletionProvider implements vscode.InlineCompletionItemProvider {
    // Timer & cancellation management
    private abortController: AbortController | null = null;
    private lastValidCompletion: vscode.InlineCompletionItem | null = null;

    // For tracking changes in the full prefix (we now focus only on the last 30 characters)
    private lastFullPrefix: string | null = null;
    private lastInvocationTime: number = 0;

    // Constants
    private readonly DEBOUNCE_DELAY = 250; // ms
    private readonly MIN_TYPING_INTERVAL = 300; // ms
    private readonly SUGGESTION_TIMEOUT = 5000; // 5 seconds

    // Cache for completions (valid for 30 seconds)
    static cache = new Map<string, { completion: string; timestamp: number }>();
    static readonly CACHE_DURATION = 30000; // ms

    // Wrap our API call in a debounced function.
    private debouncedProvide = debounce(this.provideCompletionInternal.bind(this), this.DEBOUNCE_DELAY);


    // Track the last prefix for similarity checks and typing pattern detection.
    private lastPrefix: string | null = null;
    

    async provideInlineCompletionItems(
        document: vscode.TextDocument,
        position: vscode.Position,
        context: vscode.InlineCompletionContext,
        token: vscode.CancellationToken
    ): Promise<vscode.InlineCompletionList | null> {
        console.log("Triggering completion for", document.fileName);
        return await this.debouncedProvide(document, position, token);
    }

    private async provideCompletionInternal(
        document: vscode.TextDocument,
        position: vscode.Position,
        token: vscode.CancellationToken
    ): Promise<vscode.InlineCompletionList | null> {
        const now = Date.now();
        const fullText = document.getText();
        const cursorOffset = document.offsetAt(position);
        const prefix = fullText.slice(0, cursorOffset);
        const suffix = fullText.slice(cursorOffset);
        // Throttle calls if typing too fast.
        if (now - this.lastInvocationTime < this.MIN_TYPING_INTERVAL) {
            console.log(`Typing too fast (${now - this.lastInvocationTime}ms). Skipping API call.`);
            return null;
        }
        this.lastInvocationTime = now;

        // Abort any in-flight API call.
        if (this.abortController  ) {
            console.log("Aborting previous API request.");

            this.abortController.abort();
        }
        this.abortController = new AbortController();

        // Check autocomplete quota before proceeding (assumes getStoreKey and getAccountUsage exist).
        const authToken = getStoreKey(AUTH_TOKEN);
        const userUsage = await getAccountUsage(authToken);
        if (
            userUsage?.account_usage?.autocomplete?.used_requests >=
            userUsage?.account_usage?.autocomplete?.total_requests
        ) {
            console.log("Autocompletion quota exhausted.");
            return null;
        }

        // Get prefix and suffix.
        // const fullText = document.getText();
        // const cursorOffset = document.offsetAt(position);
        // const prefix = fullText.slice(0, cursorOffset);
        // const suffix = fullText.slice(cursorOffset);
        console.log(fullText, "fullText_vscode");
        console.log(cursorOffset, "cursorOffset_vscode");
        console.log(prefix, "prefix_vscode");
        console.log(suffix, "suffix_vscode");
        
        // (a) Skip if prefix is too short (minimum non-whitespace characters).
        if (prefix.trim().length < 3) {
            console.log("Prefix too short. Skipping API call.");
            return null;
        }

        // (b) Compare only the recent part of the prefix (e.g. last 30 characters).
        const recentContextLength = 30;
        const newRecent = prefix.slice(-recentContextLength);
        // console.log(this.lastFullPrefix.slice(-recentContextLength), "oldRecent", newRecent,"newRecent");
        if (this.lastFullPrefix !== null) {
            const oldRecent = this.lastFullPrefix.slice(-recentContextLength);
            console.log(oldRecent, "oldRecent", newRecent,"newRecent");
            
            const similarity = calculateSimilarity(oldRecent, newRecent);
            if (similarity>= 0.85 && this.lastValidCompletion ) {
                console.log(`Minimal change in recent prefix (similarity=${similarity}). Skipping API call.`);
                return { items: [this.lastValidCompletion] };
            }
        }
        this.lastFullPrefix = prefix;

        // (c) Check that the last character is meaningful.
        const lastChar = prefix.trim().slice(-1);
        const meaningfulPattern = getMeaningfulPatternForLanguage(document.languageId);
        if (!meaningfulPattern.test(lastChar)) {
            console.log(`Last character "${lastChar}" not meaningful. Skipping API call.`);
            return null;
        }

        // --- Caching ---
        const cacheKey = generateCacheKey(document, cursorOffset, prefix);
        cleanupCache();
        if (AICodeCompletionProvider.cache.has(cacheKey)) {
            const cached = AICodeCompletionProvider.cache.get(cacheKey)!;
            if (now - cached.timestamp < AICodeCompletionProvider.CACHE_DURATION) {
                console.log("Returning cached completion.");
                const adjustedPosition = adjustPosition(document, position);
                const item = new vscode.InlineCompletionItem(
                    cached.completion,
                    new vscode.Range(adjustedPosition, adjustedPosition)
                );
                return { items: [item] };
            } else {
                AICodeCompletionProvider.cache.delete(cacheKey);
            }
        }

        // --- API Call ---
        const isAfterClosingBrace = document.lineAt(position.line).text.trim().endsWith("}");
        const adjustedPosition = adjustPosition(document, position);
        console.log("Making API call to OpenAI...");

        let completion: string;
        try {
            completion = await getGeminiCompletion(
                document.fileName,
                cursorOffset,
                prefix,
                suffix,
                await vscode.env.clipboard.readText(),
                document.languageId,
                {
                    // ... your options here
                    disable: false,
                    useCopyBuffer: false,
                    useFileSuffix: true,
                    maxPromptTokens: 1024,
                    prefixPercentage: 0.85,
                    maxSuffixPercentage: 0.25,
                    debounceDelay: 350,
                    multilineCompletions: "auto",
                    slidingWindowPrefixPercentage: 0.75,
                    slidingWindowSize: 500,
                    maxSnippetPercentage: 0.6,
                    recentlyEditedSimilarityThreshold: 0.3,
                    useCache: true,
                    onlyMyCode: true,
                    useOtherFiles: true,
                    useRecentlyEdited: true,
                    recentLinePrefixMatchMinLength: 7,
                    disableInFiles: undefined,
                    useImports: true,
                },
                [],
                this.abortController.signal
            );
        } catch (error: any) {
            if (error.name === "AbortError" || error.message?.includes("Request was aborted")) {
                console.log("API call aborted.");
                return null;
            } else {
                console.error("Error during API call:", error);
                return null;
            }
        }
        console.log(`[${Date.now()}] ${completion} Received API response.`);
        // Update account usage asynchronously (fire-and-forget).
        updateAccountUsage(authToken, { autocomplete: 1 }).catch((err) =>
            console.error("Error updating usage:", err)
        );

        // Respect the cancellation token before postprocessing.
        if (token.isCancellationRequested) {
            const currentPrefix = document.getText().slice(0, document.offsetAt(position));
            const similarityAfter = calculateSimilarity(currentPrefix, prefix);
            if (similarityAfter < 0.90) {
                console.log(
                    `Cancellation detected with significant change (similarity=${similarityAfter}). Discarding response.`
                );
                return null;
            } else {
                console.log(
                    `Cancellation detected but prefix similar (similarity=${similarityAfter}). Proceeding.`
                );
            }
        }

        // --- Postprocessing ---
        const processedCompletion = postprocessCompletion({
            completion,
            prefix,
            suffix,
            isAfterClosingBrace,
            languageId: document.languageId,
        });
        if (processedCompletion) {
            const item = new vscode.InlineCompletionItem(
                processedCompletion,
                new vscode.Range(adjustedPosition, adjustedPosition)
            );
            this.lastValidCompletion = item;
            return { items: [item] };
        }
        else{
            console.log("Processed completion:", processedCompletion);
            return null;
        }
    }
        // Cache the new completion.
    //     AICodeCompletionProvider.cache.set(cacheKey, {
    //         completion: processedCompletion,
    //         timestamp: Date.now(),
    //     });
    //     console.log("Caching new completion with key:", cacheKey);

    //     const item = new vscode.InlineCompletionItem(
    //         processedCompletion,
    //         new vscode.Range(adjustedPosition, adjustedPosition)
    //     );
    //     console.log("Returning inline completion.");
    //     return { items: [item] };
    // }
}

/** Helper to return a language-specific meaningful-character regex */
function getMeaningfulPatternForLanguage(languageId: string): RegExp {
    if (languageId === "python") {
        return /[a-zA-Z0-9\)\]\}\:]/; // check only last character
    } else if (["javascript", "typescript", "typescriptreact", "java", "csharp"].includes(languageId)) {
        return /[a-zA-Z0-9\)\]\}\{]/;
    }
    return /[a-zA-Z0-9\)\]\}]/;
}

/**
 * Generates a cache key based on file name, cursor offset, and a snippet of the prefix.
 */
function generateCacheKey(document: vscode.TextDocument, cursorOffset: number, prefix: string): string {
    const contextSnippet = prefix.slice(-50);
    return `${document.fileName}:${cursorOffset}:${contextSnippet}`;
}

/**
 * Cleans up expired cache entries.
 */
function cleanupCache() {
    const now = Date.now();
    for (const [key, value] of AICodeCompletionProvider.cache.entries()) {
        if (now - value.timestamp >= AICodeCompletionProvider.CACHE_DURATION) {
            AICodeCompletionProvider.cache.delete(key);
        }
    }
}

function adjustPosition(document: vscode.TextDocument, position: vscode.Position): vscode.Position {
    // Optionally adjust the insertion point (for example, skip whitespace)
    return position;
}

function calculateSimilarity(a: string, b: string): number {
    const minLen = Math.min(a.length, b.length);
    let commonLength = 0;
    for (let i = 0; i < minLen; i++) {
        if (a[i] === b[i]) {
            commonLength++;
        } else {
            break;
        }
    }
    const similarity = commonLength / Math.max(a.length, b.length);
    console.log(`Similarity between strings: ${similarity}`);
    return similarity;
}

