export class BuildCodeLensProvider implements vscode.CodeLensProvider {
    private _onDidChangeCodeLenses: vscode.EventEmitter<void> = new vscode.EventEmitter<void>();
    public readonly onDidChangeCodeLenses: vscode.Event<void> = this._onDidChangeCodeLenses.event;
    //@ts-ignore
    private language: string;
    //@ts-ignore
    private importedFiles: any;

    //@ts-ignore
    constructor(results: object) {
        //@ts-ignore
        vscode.workspace.onDidChangeConfiguration((_) => {
            this._onDidChangeCodeLenses.fire();
        });
    }

    //@ts-ignore
    public async provideCodeLenses(
        document: vscode.TextDocument,
        token: vscode.CancellationToken
    ): GCCodeLens[] | Thenable<GCCodeLens[]> {
        const activeEditor = vscode.window.activeTextEditor;
        if (!activeEditor || (activeEditor && !activeEditor.document)) return [];

        this.language = getLanguageType(activeEditor.document.languageId);
        //@ts-ignore
        const gutterIcons = [];
        //@ts-ignore
        const { detectedCode, importedCode } = await getTestableCodes(document);
        this.importedFiles = importedCode;
        const codeLenses = [];

        if (detectedCode && detectedCode.length) {
            const buildButtonPos = new vscode.Position(0, 0);
            const cl = new GCCodeLens(new vscode.Range(buildButtonPos, buildButtonPos), "");
            codeLenses.push(cl);

            detectedCode.forEach((code: any) => {
                codeLenses.push(new GCCodeLens(code.range, code));
                const pos = new vscode.Position(code.range.start.line, 0);
                gutterIcons.push({ range: new vscode.Range(pos, pos) });
            });
        }

        //@ts-ignore
        activeEditor.setDecorations(gutterDecoration, gutterIcons);
        return codeLenses;
    }

    public resolveCodeLens(codeLens: GCCodeLens, token: vscode.CancellationToken) {
        const activeEditor = vscode.window.activeTextEditor;
        if (!activeEditor) return;

        if (codeLens.component === "") {
            // CodeLens for showing the build command
            codeLens.command = {
                title: "",
                command: "gocodeo.opensidebar",
                iconPath: {
                    light: path.join(__dirname, "..", "logoonly.png"),
                    dark: path.join(__dirname, "..", "logoonly.png"),
                },
                arguments: [true],
            };
        } else {
            const componentType = getComponentType(codeLens.component.kind);
            const payload = {
                language: this.language,
                type: componentType,
                component: codeLens.component.name,
            };

            // Command for building the app
            const BuildCommandTitle = `GoCodeo-BUILD`;
            const BuildCommand: vscode.Command = {
                title: BuildCommandTitle,
                tooltip: `Choose a build option for your app`,
                command: "gocodeo.builder",
                arguments: [payload, false],
            };

            // console.log("componentType: ", componentType);

            // Set commands for CodeLens
            codeLens.command = {
                title: `${BuildCommand.title}`,
                tooltip: `Create your app using ${BuildCommand.title}`,
                command: BuildCommand.command,
                arguments: BuildCommand.arguments,
            };

            return codeLens;
        }
        return codeLens;
    }
}


