const baseURL ="https://ai-service.gocodeo.com/";


export const generateBehaviours = async (requestData, authToken) => {
  try {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 40000); // Timeout after 40 seconds
    const response = await fetch(`${baseURL}/api/v1/generate_behaviours/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `${authToken}`,
        "mode": "cors",
        "cache": "no-cache"
      },
      body: JSON.stringify(requestData),
      signal: controller.signal,
    });
    clearTimeout(timeoutId); // Clear the timeout if the request succeeds

    if (!response.ok) {
      throw new Error(`HTTP error! Status: ${response.status}`);
    }
    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Error generating behaviors:', error);
    throw error;
  }
};

