//@ts-ignore
import * as vscode from "vscode";
import { getUniqueId } from "./CommonServices";
import { INSTALLATION_ID, LOGGED_IN_KEY } from "./Constants";

export class StorageService {
    private _storage: any;
    private _type: string;
    //@ts-ignore
    constructor(storage: any, type='local') {
        this._storage = storage;
        this._type = type;
    }
    getValue(key: string) {
        return this._storage.get(key);
    }
    setValue(key: string, value: any) {
        if (this._type === 'local') {
            console.log(`Updating key2: ${key} with value: ${value}`);
             this._storage.update(key, value, vscode.ConfigurationTarget.Global);
            console.log(`Updated key2: ${key}, new value: ${this._storage.get(key)}`);
        }
        else
            this._storage.update(key, value);
    }
    getInstallationId() {
        let uid = this._storage.get(INSTALLATION_ID);
        if (!uid) {
            uid = getUniqueId();
            this._storage.update(INSTALLATION_ID, uid);
        }
        let isLoggedInBefore = this._storage.get(LOGGED_IN_KEY);
        console.log(`db key:${isLoggedInBefore}`)
        return {uid, is_new: (isLoggedInBefore ? false : true)};
    }

    clear(key: string) {
        if (key) this._storage.update(key, undefined);
        else {
            //@ts-ignore
            this._storage.keys().forEach((key) => {
                this._storage.update(key, undefined);
            });
        }
    }
}
export function getStoreKey(key: string) {
    const localConfig = vscode.workspace.getConfiguration();
    const localStore = new StorageService(localConfig);
    return localStore.getValue(key);

}