export function parseGoTestifyOutput(
    output: string,
    testTitle: string,
    testName: string
): Array<{
    testTitle: string;
    testResult: string;
    testFailureReason: string;
    executionTime?: string;
    uniqeTitle: string;
}> {
    if (!output.trim()) return [];

    const testResults = [];
    const lines = output.split('\n');
    let matched = false; // Flag to track if any known pattern matched

    lines.forEach((line) => {
       if (line.includes("--- PASS")) {
            // Test case passed
            const match = line.match(/--- PASS:\s+(.+)\s\((\d+\.\d+s)\)/);
            if (match) {
                testResults.push({
                    testTitle: match[1],
                    testResult: "passed",
                    executionTime: match[2],
                    testFailureReason: "",
                    uniqeTitle: testName,
                });
                matched = true;
            }
        } else if (line.includes("--- FAIL")) {
            // Test case failed
            const match = line.match(/--- FAIL:\s+(.+)\s\((\d+\.\d+s)\)/);
            if (match) {
                testResults.push({
                    testTitle: match[1],
                    testResult: "failed",
                    executionTime: match[2],
                    testFailureReason: output,
                    uniqeTitle: testName,
                });
                matched = true;
            }
        } else if (line.includes("FAIL")) {
            // Entire test suite failed
            const match = line.match(/FAIL\s+(.+)/);
            if (match) {
                testResults.push({
                    testTitle: match[1],
                    testResult: "failed",
                    testFailureReason: output,
                    uniqeTitle: testName,
                });
                matched = true;
            }
        } else if (line.includes("PASS")) {
            // Entire test suite passed
            const match = line.match(/PASS\s+(.+)/);
            if (match) {
                testResults.push({
                    testTitle: match[1],
                    testResult: "passed",
                    testFailureReason: "",
                    uniqeTitle: testName,
                });
                matched = true;
            }
        }
    });

    // Fallback for unrecognized output
    if (!matched) {
        testResults.push({
            testTitle: "Unrecognized Test Output",
            testResult: "failed",
            testFailureReason: output, // Include the entire output for debugging
            uniqeTitle: testName,
        });
    }

    console.log(testResults, "parseGoTestifyOutput");

    return testResults;
}
 
export function parseGoGinkgoOutput(
    output: string,
    testTitle: string,
    testName: string
): Array<{
    testTitle: string;
    testResult: string;
    testFailureReason: string;
    executionTime?: string;
    uniqeTitle: string;
}> {
    console.log(output, "ginkgoOutput");

    // Strip ANSI color codes using a regex
    const cleanedOutput = output.replace(/\x1b\[[0-9;]*m/g, '');

    if (!cleanedOutput.trim()) return [];

    const testResults = [];
    const lines = cleanedOutput.split("\n");
    let matched = false; // Flag to track if any known pattern matched

    lines.forEach((line) => {
        if (line.includes("[PASS]")) {
            const match = line.match(/\[PASS\]\s+(\S+)\s+(\d+\.\d+s)/);
            if (match) {
                testResults.push({
                    testTitle: match[1],
                    testResult: "passed",
                    executionTime: match[2],
                    testFailureReason: "",
                    uniqeTitle: testName,
                });
                matched = true;
            }
        } else if (line.includes("[FAIL]")) {
            const match = line.match(/\[FAIL\]\s+(\S+)\s+(\d+\.\d+s)/);
            if (match) {
                testResults.push({
                    testTitle: match[1],
                    testResult: "failed",
                    executionTime: match[2],
                    testFailureReason: cleanedOutput,
                    uniqeTitle: testName,
                });
                matched = true;
            }
        } else if (line.includes("Test Suite Passed")) {
            testResults.push({
                testTitle: testTitle || "Test Suite",
                testResult: "passed",
                executionTime: undefined,
                testFailureReason: "",
                uniqeTitle: testName,
            });
            matched = true;
        }
    });

    if (!matched) {
        testResults.push({
            testTitle: "Unrecognized Test Output",
            testResult: "failed",
            testFailureReason: cleanedOutput,
            uniqeTitle: testName,
        });
    }

    return testResults;
}



export function parseJunitTestOutput(
    output: string,
    testTitle: string,
    testName: string
): Array<{
    testTitle: string;
    testResult: string;
    testFailureReason: string;
    executionTime?: string;
    uniqeTitle: string;
}> {
    if (!output.trim()) return [];

    const testResults = [];
    const lines = output.split('\n');
    let matched = false; // Flag to track if any known pattern matched

    // Check for Maven POM-related errors
    if (output.includes("there is no POM in this directory")) {
        testResults.push({
            testTitle: "Maven Configuration Error",
            testResult: "error",
            testFailureReason: "No POM file found in the specified directory.",
            uniqeTitle: testName,
        });
        console.log(testResults, "parseJunitTestOutput - POM Error");
        return testResults;
    }

    lines.forEach((line) => {
        // Match for individual test results (Passed)
        if (line.includes("<testcase")) {
            const matchPass = line.match(/<testcase.*name="([^"]+)".*time="([^"]+)".*\/>/);
            if (matchPass) {
                testResults.push({
                    testTitle: matchPass[1],
                    testResult: "passed",
                    executionTime: matchPass[2],
                    testFailureReason: "",
                    uniqeTitle: testName,
                });
                matched = true;
            }
        } else if (line.includes("<failure")) {
            // Test case failed
            const matchFail = line.match(/<testcase.*name="([^"]+)".*time="([^"]+)".*><failure.*message="([^"]+)"/);
            if (matchFail) {
                testResults.push({
                    testTitle: matchFail[1],
                    testResult: "failed",
                    executionTime: matchFail[2],
                    testFailureReason: matchFail[3],
                    uniqeTitle: testName,
                });
                matched = true;
            }
        } else if (line.includes("<error")) {
            // Test case error
            const matchError = line.match(/<testcase.*name="([^"]+)".*time="([^"]+)".*><error.*message="([^"]+)"/);
            if (matchError) {
                testResults.push({
                    testTitle: matchError[1],
                    testResult: "failed",
                    executionTime: matchError[2],
                    testFailureReason: matchError[3],
                    uniqeTitle: testName,
                });
                matched = true;
            }
        }
    });

    // Fallback for unrecognized output
    if (!matched) {
        testResults.push({
            testTitle: "Unrecognized Test Output",
            testResult: "failed",
            testFailureReason: output, // Include the entire output for debugging
            uniqeTitle: testName,
        });
    }

    console.log(testResults, "parseJunitTestOutput");

    return testResults;
}

export function parseMochaTestOutput(output: string, testTitle: string, testName: string): Array<any> {
    if (!output.trim()) return [];
    console.log(testName, "parseMochaTestOutputTestName");

    const testResults:any = [];
    const lines = output.split("\n");
    let hasFailingTests = false; // Flag to track if there are failing tests
    let matched = false; // Flag to track if any known pattern matched

    lines.forEach((line) => {
        // Parse passing tests
        if (line.trim().startsWith("")) {
            const match = line.match(/ (.+)/);
            if (match) {
                const testTitleName = match[1];
                testResults.push({
                    testTitle: testTitleName,
                    testResult: "passed",
                    executionTime: "", // Execution time is summarized later
                    testFailureReason: "",
                    uniqeTitle: testName,
                });
                matched = true; // Set matched to true for this line
            }
        }

        // Parse summary line for execution time
        if (line.trim().endsWith("passing")) {
            const match = line.match(/(\d+) passing \((\d+ms)\)/);
            if (match) {
                const executionTime = match[2];
                testResults.forEach((result: any) => {
                    if (result.testResult === "passed") {
                        result.executionTime = executionTime; // Assign execution time to passed tests
                    }
                });
                matched = true; // Set matched to true for this line
            }
        }

        // Parse failing tests (AssertionError or other failures)
        if (line.trim().startsWith("") || line.includes("AssertionError")) {
            hasFailingTests = true; // Set flag to true if there is a failing test
            const match = line.match(/ (.+)/) || line.match(/(.+): AssertionError/);
            const testTitleName = match ? match[1] : testTitle; // Default to testTitle if no match
            testResults.push({
                testTitle: testTitleName,
                testResult: "failed",
                executionTime: "", // Add detailed time if available in future
                testFailureReason: output, // Attach the entire output for failing tests
                uniqeTitle: testName,
            });
            matched = true; // Set matched to true for this line
        }
        if (line.includes("SyntaxError") && !line.includes("AssertionError")) {
            testResults.push({
                testTitle: "Test Suite Error",
                testResult: "failed",
                testFailureReason: output,
                uniqeTitle: testName,
            });
            matched = true; // Set matched to true for this line
        }
        if (line.includes("ReferenceError") && !line.includes("AssertionError")) {
            testResults.push({
                testTitle: "Test Suite Error",
                testResult: "failed",
                testFailureReason: output,
                uniqeTitle: testName,
            });
            matched = true; // Set matched to true for this line
        }
        if (line.includes("TypeError") || line.includes("RangeError") || line.includes("Timeout of") || line.includes("UnhandledPromiseRejection") || line.includes("Cannot find module") || line.includes("DeprecationWarning") || line.includes("Unhandled event")) {
            testResults.push({
                testTitle: "Test Suite Error",
                testResult: "failed",
                testFailureReason: output,
                uniqeTitle: testName,
            });
            matched = true;
        }
        

        // Parse suite-level errors (like syntax errors, runtime issues)
        if (line.includes("Error") && !line.includes("AssertionError")) {
            testResults.push({
                testTitle: "Test Suite Error",
                testResult: "error",
                testFailureReason: output,
                uniqeTitle: testName,
            });
            matched = true; // Set matched to true for this line
        }
    });

    // Add a fallback if no patterns matched
    if (!matched) {
        testResults.push({
            testTitle: "Unrecognized Test Output",
            testResult: "failed",
            executionTime: "", // Optional: Extract total time if present
            testFailureReason: output, // Include the entire test run output for debugging
            uniqeTitle: testName,
        });
    }

    // If there are any failing tests, ensure the whole output is captured in the result
    if (hasFailingTests) {
        testResults.push({
            testTitle: "Test Run Failed",
            testResult: "failed",
            executionTime: "", // Optional: Extract total time if present
            testFailureReason: output, // Include the entire test run output for debugging
            uniqeTitle: testTitle,
        });
    }

    console.log(testResults, "parseMochaTestOutput");
    return testResults;
}

export function parseJSTestOutput(output, testTitle, testName) {
    if (!output.trim()) return [];

    const testResults = [];
    const lines = output.split('\n');
    let isConfigError = false;
    let matched = false; // Flag to track if any known pattern matched

    // Check for configuration-related errors
    if (output.includes('Could not find a config file')) {
        isConfigError = true;
        testResults.push({
            testTitle: 'Config Error',
            testResult: 'error',
            testFailureReason: 'Jest configuration file not found or invalid.',
            uniqeTitle: testName,
        });
        matched = true; // Mark as matched since this is a known case
    }

    // If it's not a config error, parse the test output normally
    if (!isConfigError) {
        lines.forEach((line) => {
            if (line.startsWith('FAIL')) {
                const match = line.match(/FAIL (.*)/);
                if (match) {
                    const testFile = match[1];
                    testResults.push({
                        testTitle: testFile,
                        testResult: 'failed',
                        testFailureReason: output,
                        uniqeTitle: testName,
                    });
                    matched = true; // Mark as matched
                }
            } else if (line.startsWith('')) {
                const match = line.match(/ (.*) \((.*)\)/);
                if (match) {
                    const testTitleName = match[1];
                    const time = match[2];
                    testResults.push({
                        testTitle: testTitleName,
                        testResult: 'failed',
                        executionTime: time,
                        testFailureReason: output,
                        uniqeTitle: testName,
                    });
                    matched = true; // Mark as matched
                }
            } else if (line.startsWith('')) {
                const match = line.match(/ (.*) \((.*)\)/);
                if (match) {
                    const testTitleName = match[1];
                    const time = match[2];
                    testResults.push({
                        testTitle: testTitleName,
                        testResult: 'passed',
                        executionTime: time,
                        testFailureReason: '',
                        uniqeTitle: testName,
                    });
                    matched = true; // Mark as matched
                }
            } else if (line.includes('PASS')) {
                const match = line.match(/PASS (.*)/);
                if (match) {
                    const suiteName = match[1];
                    testResults.push({
                        testTitle: suiteName,
                        testResult: 'passed',
                        testFailureReason: '',
                        uniqeTitle: testName,
                    });
                    matched = true; // Mark as matched
                }
            } else if (line.includes('Your test suite must contain at least one test.')) {
                testResults.push({
                    testTitle: 'Test Suite',
                    testResult: 'failed',
                    testFailureReason: 'No tests defined',
                    uniqeTitle: testName,
                });
                matched = true; // Mark as matched
            } else if (line.includes("TypeError") || line.includes("RangeError") || line.includes("Timeout of") || line.includes("UnhandledPromiseRejection") || line.includes("Cannot find module") || line.includes("DeprecationWarning") || line.includes("Unhandled event")) {
                testResults.push({
                    testTitle: "Test Suite Error",
                    testResult: "failed",
                    testFailureReason: output,
                    uniqeTitle: testName,
                });
                matched = true;
            }
        });
    }

    // Fallback for unrecognized outputs
    if (!matched) {
        testResults.push({
            testTitle: 'Unrecognized Test Output',
            testResult: 'failed',
            executionTime: '', // Optionally extract total time
            testFailureReason: output, // Include the entire output for debugging
            uniqeTitle: testName,
        });
    }

    console.log(testResults, "parseJSTestOutput");

    return testResults;
}

export function parsePythonPytestOutput(output, testTitle, testName) {
    console.log("Parsing output:", output);

    if (output.trim() === "") {
        return [];
    }

    const lines = output.split("\n");
    const testResults = [];
    let matched = false; // Track if any line matches the expected patterns
    let i = 0;

    while (i < lines.length) {
        const line = lines[i].trim();
        console.log("Line:", line);

        // Check for passed test
        const passedMatch = line.match(/^(.*) PASSED.*$/);
        if (passedMatch) {
            const testTitleName = passedMatch[1].trim(); // Get the test case name
            testResults.push({
                testTitle: testTitleName,
                testResult: "passed",
                testFailureReason: "",
                uniqeTitle: testName,
            });
            matched = true;
            i++;
            continue;
        }

        // Check for failed test
        const failedMatch = line.match(/^(.*) FAILED.*$/);
        if (failedMatch) {
            const testTitleName = failedMatch[1].trim(); // Get the test case name
            testResults.push({
                testTitle: testTitleName,
                testResult: "failed",
                testFailureReason: output.trim(), // Attach the entire output for failed tests
                uniqeTitle: testName,
            });
            matched = true;
            i++;
            continue;
        }

        // Check for error details
        const errorMatch = line.match(/^(.*) ERROR .*$/);
        if (errorMatch) {
            const testTitleName = errorMatch[1].trim(); // Get the test case name
            testResults.push({
                testTitle: testTitleName,
                testResult: "failed",
                testFailureReason: output.trim(), // Attach the entire output for errors
                uniqeTitle: testName,
            });
            matched = true;
            i++;
            continue;
        }

        i++;
    }

    // Fallback: If no matches were found, return the entire output as a failure
    if (!matched) {
        testResults.push({
            testTitle: testTitle || "Unknown Test", // Use a generic title if none provided
            testResult: "failed",
            testFailureReason: output.trim(),
            uniqeTitle: testName,
        });
    }

    console.log(testResults, "testResultsXXX");
    return testResults;
}

export function parsePythonUnittestOutput(output, testTitle, testName) {
    console.log("Parsing unittest output:", output);

    if (output.trim() === "") {
        return [];
    }

    const lines = output.split("\n");
    const testResults = [];
    let matched = false; // Track if any line matches the expected patterns
    let i = 0;

    // Handle the case where the output contains a general setup or import failure (e.g., NameError)
    const importErrorMatch = output.match(/NameError: name '([^']+)' is not defined/);
    if (importErrorMatch) {
        testResults.push({
            testTitle: testTitle || "Import Error", // Use a generic title if testTitle is undefined
            testResult: "failed",
            testFailureReason: output.trim(), // Attach the entire output for import errors
            uniqeTitle: testName,
        });
        return testResults; // Return early since this is a setup error
    }

    // Handle cases where no tests were found or executed
    if (output.includes("NO TESTS RAN") || output.includes("Ran 0 tests")) {
        testResults.push({
            testTitle: "No tests found or ran",
            testResult: "skipped",
            testFailureReason:
                "No test cases were discovered or executed. Ensure the test methods are defined correctly and follow naming conventions.",
            uniqeTitle: testName,
        });
        return testResults;
    }

    while (i < lines.length) {
        const line = lines[i].trim();
        console.log("Line:", line);

        // Check for passed test
        const passedMatch = line.match(/^(.*) \.\.\. ok$/); // Matches "test_case_name ... ok"
        if (passedMatch) {
            const testTitleName = passedMatch[1].trim(); // Get the test case name
            testResults.push({
                testTitle: testTitleName,
                testResult: "passed",
                testFailureReason: "",
                uniqeTitle: testName,
            });
            matched = true;
            i++;
            continue;
        }

        // Check for failed test
        const failedMatch = line.match(/^(.*) \.\.\. FAIL$/); // Matches "test_case_name ... FAIL"
        if (failedMatch) {
            const testTitleName = failedMatch[1].trim(); // Get the test case name
            testResults.push({
                testTitle: testTitleName,
                testResult: "failed",
                testFailureReason: output.trim(), // Attach the entire output for failed tests
                uniqeTitle: testName,
            });
            matched = true;
            i++;
            continue;
        }

        // Check for errors
        const errorMatch = line.match(/^(.*) \.\.\. ERROR$/); // Matches "test_case_name ... ERROR"
        if (errorMatch) {
            const testTitleName = errorMatch[1].trim(); // Get the test case name
            testResults.push({
                testTitle: testTitleName,
                testResult: "failed",
                testFailureReason: output.trim(), // Attach the entire output for errors
                uniqeTitle: testName,
            });
            matched = true;
            i++;
            continue;
        }

        i++;
    }

    // Fallback: If no matches were found, return the entire output as a failure
    if (!matched) {
        testResults.push({
            testTitle: testTitle || "Unknown Test", // Use a generic title if none provided
            testResult: "failed",
            testFailureReason: output.trim(), // Attach the full output for debugging
            uniqeTitle: testName,
        });
    }

    console.log(testResults, "testResultsUnittest");
    return testResults;
}