import { log } from "console";
import { lineIsRepeated } from "./charStream";


function longestCommonSubsequence(a: string, b: string) {
  const lengths: number[][] = [];
  for (let i = 0; i <= a.length; i++) {
    lengths[i] = [];
    for (let j = 0; j <= b.length; j++) {
      if (i === 0 || j === 0) {
        lengths[i][j] = 0;
      } else if (a[i - 1] === b[j - 1]) {
        lengths[i][j] = lengths[i - 1][j - 1] + 1;
      } else {
        lengths[i][j] = Math.max(lengths[i - 1][j], lengths[i][j - 1]);
      }
    }
  }
  let result = "";
  let x = a.length;
  let y = b.length;
  while (x !== 0 && y !== 0) {
    if (lengths[x][y] === lengths[x - 1][y]) {
      x--;
    } else if (lengths[x][y] === lengths[x][y - 1]) {
      y--;
    } else {
      result = a[x - 1] + result;
      x--;
      y--;
    }
  }
  return result;
}


function rewritesLineAbove(completion: string, prefix: string): boolean {
  const lineAbove = prefix
    .split("\n")
    .filter((line) => line.trim().length > 0)
    .slice(-1)[0];
  if (!lineAbove) {
    return false;
  }

  const firstLineOfCompletion = completion
    .split("\n")
    .find((line) => line.trim().length > 0);
  if (!firstLineOfCompletion) {
    return false;
  }
  return lineIsRepeated(lineAbove, firstLineOfCompletion);
}

const MAX_REPETITION_FREQ_TO_CHECK = 3;
function isExtremeRepetition(completion: string): boolean {
  const lines = completion.split("\n");
  if (lines.length < 6) {
    return false;
  }
  for (let freq = 1; freq < MAX_REPETITION_FREQ_TO_CHECK; freq++) {
    const lcs = longestCommonSubsequence(lines[0], lines[freq]);
    if (lcs.length > 5 || lcs.length > lines[0].length * 0.5) {
      let matchCount = 0;
      for (let i = 0; i < lines.length; i += freq) {
        if (lines[i].includes(lcs)) {
          matchCount++;
        }
      }
      if (matchCount * freq > 8 || (matchCount * freq) / lines.length > 0.8) {
        return true;
      }
    }
  }
  return false;
}
export function postprocessCompletion({
  completion,
  prefix,
  suffix,
  isAfterClosingBrace,
  languageId
}: {
  completion: string;
  prefix: string;
  suffix: string;
  languageId: string;
  isAfterClosingBrace: boolean;
}): string | undefined {
  console.log("hello there i am from postprocessCompletion", completion);

  // Remove leading/trailing whitespace.
  let processedCompletion = completion.trim();
  if (processedCompletion.length === 0) {
    return undefined;
  }

  // Instead of discarding completions that rewrite the line above,
  // remove the duplicate part.
  if (rewritesLineAbove(processedCompletion, prefix)) {
    console.log("Detected repeated line above. Removing duplicate prefix from completion.");

    // Get the last non-empty line from the prefix.
    const prefixLines = prefix.split("\n").filter(line => line.trim().length > 0);
    const lastPrefixLine = prefixLines[prefixLines.length - 1] || "";

    // Split the completion into lines.
    const completionLines = processedCompletion.split("\n");
    const firstLine = completionLines[0].trim();

    // If the first line of the completion starts with the prefix's last line,
    // remove that repeated part.
    if (firstLine.startsWith(lastPrefixLine.trim())) {
      // Remove exactly the repeated part from the beginning of the first line.
      completionLines[0] = completionLines[0].slice(lastPrefixLine.length);
      // Reconstruct the completion.
      processedCompletion = completionLines.join("\n").trim();
    }
  }

  // Check for extreme repetition as before.
  if (isExtremeRepetition(processedCompletion)) {
    return undefined;
  }

  console.log("postprocessCompletion: returning processed completion: ", processedCompletion);
  return processedCompletion;
}


