export const fileContentsForFlaskProject = [
    {
        filename: "run.py",
        content: `
from app import create_app

app = create_app()

if __name__ == "__main__":
    app.run(debug=True)

`,
    },
    {
        filename: "requirements.txt",
        content: `Flask==2.3.2
`,
    },
      
    {
        filename: "app",
        contents: [
            {
                filename: "__init__.py",
                content: `from flask import Flask

def create_app():
    app = Flask(__name__)
    
    # Register routes
    with app.app_context():
        from .routes import bp
        app.register_blueprint(bp)
    
    return app
`,
            },
            {
                filename: "routes.py",
                content: `from flask import Blueprint

bp = Blueprint('main', __name__)

@bp.route('/')
def hello_world():
    return "Hello, world!"

`,
            },
            
        ],
    },
    
];

export default fileContentsForFlaskProject;
