async function addTabBeforeNewline(inputString) {
  console.log("Original Input String:", inputString);

  const mainBlockPattern = /unittest\.main\(\)/;
  const mainPattern = /if __name__ == '__main__':/;

  // Remove unnecessary blocks
  inputString = inputString.replace(mainBlockPattern, "");
  console.log("After Removing unittest.main():", inputString);

  inputString = inputString.replace(mainPattern, "");
  console.log("After Removing if __name__ == '__main__':", inputString);

  const lines = inputString?.split("\n");
  console.log("Lines Array:", lines);

  let insideClass = false;

  // Add tab character before each line that should be indented
  for (let i = 0; i < lines.length; i++) {
    console.log(`Processing Line ${i}:`, lines[i]);

    // If we are at the class definition line, don't add extra tabs
    if (lines[i].startsWith("class ")) {
      insideClass = true;
      console.log("Inside Class Definition");
      continue; // Skip indentation for class declaration
    }

    if (insideClass && lines[i].startsWith("def ")) {
      // Only indent function definitions and further nested blocks
      lines[i] = "\t" + lines[i];
      console.log("Indented Function Definition:", lines[i]);
    } else {
      // For other lines, indent them as usual
      lines[i] = "\t" + lines[i];
      console.log("Indented Line:", lines[i]);
    }
  }

  const result = lines.join("\n");
  console.log("Final Result:", result);

  return result;
}

function addSpaceBeforeNewline(inputString) {
  const mainBlockPattern = /unittest\.main\(\)/g;
  const mainPattern = /if __name__ == '__main__':/g;

  // Remove specific patterns related to main blocks
  inputString = inputString
    .replace(mainBlockPattern, "")
    .replace(mainPattern, "");

  // Split the inputString by newline character '\n'
  const lines = inputString?.split("\n");

  // Remove unnecessary empty lines
  const filteredLines = lines.filter((line) => line.trim() !== "");

  // Join the lines back together with newline characters '\n'
  const resultString = filteredLines.join("\n");

  return resultString;
}



async function detectProjectType(projectPath: string) {
  console.log("Detecting project type...");
  // Detect framework based on project structure
  const frameworkDetectors = [
    {
      name: 'React',
      check: () => fs.existsSync(path.join(projectPath, 'package.json')) &&
        fs.readFileSync(path.join(projectPath, 'package.json'), 'utf8').includes('react')
    },
    {
      name: 'Vue',
      check: () => fs.existsSync(path.join(projectPath, 'package.json')) &&
        fs.readFileSync(path.join(projectPath, 'package.json'), 'utf8').includes('vue')
    },
    {
      name: 'Next.js',
      check: () => fs.existsSync(path.join(projectPath, 'package.json')) &&
        fs.readFileSync(path.join(projectPath, 'package.json'), 'utf8').includes('next')
    },

    // Add more framework detectors
  ];

  return frameworkDetectors.find(framework => framework.check())?.name || 'React';
}


function extractProjectFolder(message: string): string {
  console.log("Extracting project folder...", message);
  const cdMatch = message.match(/cd\s+(\w+[-\w]*)/);
  return cdMatch ? cdMatch[1] : '';
}

async function prepareDeploymentPackage(projectFolder: string, panel: vscode.WebviewPanel) {
  try {
    console.log("Preparing deployment package...", projectFolder);
    panel.webview.postMessage({
      type: "buildStart",
      data: { timestamp: Date.now() }
    });

    const projectPath = projectFolder;
    const frameworkType = await detectProjectType(projectPath);
    console.log("Framework Type:", frameworkType);

    const buildCommands = {
      'React': 'npm run build',
      'Vue': 'npm run build',
      'Next.js': 'npm run build && npm run export',
      'static': 'echo "No build step required"'
    };

    console.log("Executing command", buildCommands[frameworkType], "path", projectPath);

    // Execute build command
    execSync(buildCommands[frameworkType], { cwd: projectPath });

    // Send message that build is complete
    panel.webview.postMessage({
      type: "buildComplete",
      data: {
        timestamp: Date.now(),
        frameworkType: frameworkType
      }
    });


    const buildDir = path.join(projectFolder, 'build');
    const distDir = path.join(projectFolder, 'dist');
    const outDir = path.join(projectFolder, 'out');

    let buildPath;

    if (fs.existsSync(buildDir)) {
      buildPath = buildDir;
    } else if (fs.existsSync(distDir)) {
      buildPath = distDir;
    }else if (fs.existsSync(outDir)) {
      buildPath = outDir;
    }
    else {
      let error = "Build output folder not found. Please ensure the build process generates the correct folder (build, dist, or out)."
      console.log("deployErrorLog1",error);
      panel.webview.postMessage({
        type: "buildError",
        data: {
          error: error,
          timestamp: Date.now()
        }
      });
      return null;
    }
    console.log("Build Path", buildPath);

    return buildPath;
  } catch (error) {
    console.log("deployErrorLog2", error);
    // Send build error message
    panel.webview.postMessage({
      type: "buildError",
      data: {
        error: error.message,
        timestamp: Date.now()
      }
    });
    throw error;
  }
}

async function getSiteUrl(projectName: string): Promise<string> {
  const myHeaders = new Headers();
  myHeaders.append("Authorization", `Bearer ${VERCEL_TOKEN}`);
  myHeaders.append("Content-Type", "application/json");

  const requestOptions = {
    method: "GET",
    headers: myHeaders,
    redirect: "follow",
  };

  try {
    const response = await fetch(
      `https://api.vercel.com/v9/projects/${projectName}`,
      requestOptions
    );

    if (!response.ok) {
      console.error(`Failed to fetch site URL: ${response.statusText}`);
      return `https://${projectName}.vercel.app`;
    }

    const result = await response.json(); // Parse as JSON
    console.log("245 API Result:", result);
    console.log("246 API Result:", result?.targets?.production);
    const siteUrl = result?.targets?.production?.alias?.[0]; // Adjust based on API response structure
    if (!siteUrl) {
      return `https://${projectName}.vercel.app`;
    }
    return `https://${siteUrl}`;
  } catch (error) {
    console.error("Error fetching site URL:", error);
    return `https://${projectName}.vercel.app`;
  }
}
 

async function deployToVercel(projectPath: string, rootDir: string, panel: vscode.WebviewPanel) {
  try {
    console.log("Deploying to Vercel...", projectPath, rootDir);

    const projectFolder = rootDir.split('/').length == 1
      ? rootDir.split('\\').pop()
      : rootDir.split('/').pop();
    
    // Send message that deployment is starting
    panel.webview.postMessage({
      type: "deployStart",
      data: {
        projectFolder: projectFolder,
        timestamp: Date.now()
      }
    });

    const buildDir = await prepareDeploymentPackage(rootDir, panel);
    console.log("Deployment package prepared successfully", buildDir);

    const projectName = `${projectFolder}-deployment`.toLowerCase().replace('_', '-');
    const deployCommand = [
      `npx vercel deploy ${buildDir} --prod`,
      `--token=${VERCEL_TOKEN}`,
      '--yes',
      `--name=${projectName}`,
    ].join(' ');

    console.log("deployCommand", deployCommand);

    const deploymentOutput = execSync(deployCommand, {
      cwd: projectPath,
      encoding: 'utf8'
    });

    console.log("DEPLOYED SUCCESSFULLY", deploymentOutput);

    // Extract deployment URL
    const deploymentUrl = deploymentOutput.match(/https:\/\/[^\s]+/)?.[0];

    // Send message that deployment is complete
    panel.webview.postMessage({
      type: "deployComplete",
      data: {
        deploymentUrl: deploymentUrl,
        projectName: projectName,
        timestamp: Date.now()
      }
    });

    const siteUrl = await getSiteUrl(projectName);
    console.log("siteUrl", siteUrl);
    return siteUrl;
  } catch (error) {
    console.log("deployErrorLog3");

    // Extract error message
    const errorMessage = error.message || "Unknown deployment error";

      // Check if error is due to EPERM which might indicate a running Next.js process
    if (errorMessage.includes("EPERM")) {
      vscode.window.showErrorMessage(
        "Deployment failed: It looks like your Next.js app is still running in the terminal. Please stop the app and try again."
      );
    } else {
      vscode.window.showErrorMessage(`Deployment failed: ${errorMessage}`);
    }
    console.error(errorMessage, "deployErrorLog34");

    return errorMessage; // Return error message instead of null
  }
}

