async function refreshSupabaseToken(panel: vscode.WebviewPanel): Promise<string | null> {
  try {
    const localConfig = vscode.workspace.getConfiguration();
    const localStore = new StorageService(localConfig);
    const supabaseRefreshToken = await localStore.getValue("gocodeo.supabaseRefreshToken");
    console.log("Trying supabase token refresh with refresh token", supabaseRefreshToken);
    const requestOptions = {
      method: "GET"
    };

    const response = await fetch(`${BASE_URL}/api/v1/supabase-token-refresh?refresh_token=${supabaseRefreshToken}`, requestOptions);
    console.log("supabase token refresh response", response)
    if (!response.ok) throw new Error("Failed to refresh token");
    const data = await response.json();
    console.log("supabase refresh token result", data)
    if (data.access_token && data.refresh_token) {
      console.log("Updating supabase access and refresh tokens", data.access_token, data.refresh_token)
      await localStore.setValue("gocodeo.supabaseAccessToken", data.access_token);
      await localStore.setValue("gocodeo.supabaseRefreshToken", data.refresh_token);
      panel.webview.postMessage({ command: "supabaseTokenRefreshed", data });
      return data.access_token;
    }
    return "";
  } catch (error) {
    console.error("Error refreshing token:", error);
    return null;
  }
}


async function fetchSupabaseProjects(supabaseToken: string, panel: vscode.WebviewPanel) {
  try {
    console.log("Fetching supabase projects with token", supabaseToken)
    let headers = new Headers();
    headers.append("Content-Type", "application/json");
    headers.append("Authorization", `Bearer ${supabaseToken}`);
    const raw = "";
    let newToken = "";
    let requestOptions = {
      method: "GET",
      headers: headers
    };

    let response = await fetch("https://api.supabase.com/v1/projects", requestOptions);

    // Token refresh logic
    if (response.status === 401) {
      console.log("Token expired, attempting to refresh token");
      newToken = await refreshSupabaseToken(panel);
      console.log("Refreshed supabase token, retrying with token", newToken);
      if (newToken) {
        console.log("Fetching supabase projects")
        requestOptions.headers.set("Authorization", `Bearer ${newToken}`);

        console.log("supabase project fetching response with new RequestOptions", requestOptions)
        response = await fetch("https://api.supabase.com/v1/projects", requestOptions);
        console.log("supabase project fetch response with new token", response, "newRequestOptions", requestOptions)
      } else {
        throw new Error("Failed to refresh token");
      }
    }
    const supabaseProjects = await response.json();
    console.log("supabase API response", supabaseProjects)
    if (!response.ok) throw new Error("Failed to fetch projects");
    return { projects: supabaseProjects, newToken: newToken };
  } catch (error) {
    console.error("Error fetching projects:", error);
    vscode.window.showErrorMessage("Failed to fetch Supabase projects.");
    return [];
  }
};


async function executeSupabaseSQL(sqlCommands, supabaseProjectID, supabaseProjectName, supabaseToken) {
  let sqlResult = '';
  try {
    console.log("Executing Supabase SQL commands", sqlCommands, "\nsupabase project ID", supabaseProjectID, supabaseProjectName, supabaseToken);
    const headers = new Headers();
    headers.append("Content-Type", "application/json");
    headers.append("Authorization", `Bearer ${supabaseToken}`);
    const raw = JSON.stringify({ "query": sqlCommands });
    const requestOptions = {
      method: "POST",
      headers: headers,
      body: raw,
      redirect: "follow"
    };
    console.log("Supabase SQL Execution Request", requestOptions);
    const response = await fetch(`https://api.supabase.com/v1/projects/${supabaseProjectID}/database/query`, requestOptions);
    sqlResult = await response.json();
    console.log("Supabase SQL Execution Response", sqlResult);

    // if(sqlResult.message.replace(" ", "").includes('ERROR:42710')) {
    //   vscode.window.showInformationMessage("\"profiles\" already exists!");
    // } else {
    //   vscode.window.showErrorMessage("Failed to execute Supabase SQL. Please try again.");
    // }
    if (!response.ok) throw new Error("Failed to execute Supabase SQL");
    vscode.window.showInformationMessage("Supabase SQL successfully executed.")
    return sqlResult;
  } catch (error) {
    console.error("Error executing Supabase SQL:", error);
    console.error("executeSupabaseSQLDone ext 22 :", sqlResult.message.includes('42710'), "\n ", sqlResult);
    if (sqlResult.message && (sqlResult.message.includes('42710') || sqlResult.message.includes('already exists'))) {
      // Extract the actual error message after ERROR: 42710:
      const errorMessage = sqlResult.message.split('42710:')[1].trim();
      const formattedMessage = `Supabase SQL execution skipped: ${errorMessage}.`;
      vscode.window.showInformationMessage(formattedMessage);
    } else {
      const errorRegex = /ERROR:\s+\d+:\s+(.*)/;
      const match = sqlResult.message.match(errorRegex);
      console.log("supabase sql error message regex match", match)
      if (match) {
        const errorMessage = match[1].trim();
        const formattedMessage = `Failed to Execute Supabase SQL: ${errorMessage}.`;
        vscode.window.showErrorMessage(formattedMessage);
      } else {
        vscode.window.showErrorMessage("Failed to execute Supabase SQL. Please try running the commands on the SQL editor.");
      }
    }

    return [];
  }


}

