export async function runTestsFile(
  filePath: string,
  fileName: string,
  fileText: string,
  language: string,
  framework: string,
  testTitle: string,
  sourceCode: { imports: any[], main: { file: string, code: string } },
  testName: string, className: string
): Promise<Array<any>> {
  return new Promise(async (resolve, reject) => {
    console.log(
      "Running tests for ",
      filePath,
      language,
      framework,
      testTitle,
    );
    console.log(sourceCode, fileText, "thing wil be written on temp file")
    console.log(testName, "testName file")
    console.log(className, "className file")

    // Generate unique test file name per test case
    let baseFileName = fileName.replace(/\.(js|ts|py|go|java)$/, "");
    console.log(baseFileName, "baseFileName1");
    // let testFileName = language.toLowerCase() === "python" ? `${baseFileName}_${testTitle}.py` : `${baseFileName}_${testTitle}.test.js`; 
    // Sanitize the baseFileName to remove invalid characters for file names
    baseFileName = baseFileName.replace(/[^\w-]/g, "_"); 
    console.log(baseFileName, "baseFileName2");
    const UniqueId = getUniqueId()
    let testFileName = ``;
    switch (language.toLowerCase()) {
      case "python":
        testFileName = `${baseFileName}_${testTitle}.py`;
        break;
      case "javascript":
        testFileName = `${baseFileName}_${testTitle}.test.js`;
        break;
      case "typescript":
        // testFileName = `${baseFileName}_${testTitle}.test.ts`;
        testFileName = `${baseFileName}_${UniqueId}.test.ts`;
        break;
      case "go":
        testFileName = `${baseFileName}_test.go`; // Consider adding a unique identifier if needed
        break;
      case "java":
        testFileName = `${className}.java`;
        // Add more cases for other languages as needed
        break;
      default:
        throw new Error(`Unsupported language: ${language}`);
        1
    } 
    console.log(filePath, "..", testFileName);
    
    const testFilePath = path.join(filePath, "..", testFileName);

    console.log("Generated test file path:", testFilePath);
    // Modify source code if it's JavaScript
    let modifiedCode = "";
    if (language.toLowerCase() === "javascript" || language.toLowerCase() === "typescript") {
      // await fs.promises.writeFile(testFilePath, combinedFileText);
      modifiedCode = modifyJSCode(sourceCode);
    } else if (language.toLowerCase() === "go" && framework.toLowerCase() === "testify" ) {
      modifiedCode = modifyGoCode(sourceCode);
    }

    console.log(modifiedCode, "modifiedCode");

    // Write the test file
    try {
      if (language.toLowerCase() === "javascript" || language.toLowerCase() === "typescript") {
        const combinedFileText = modifiedCode + '\n' + fileText;
        await fs.promises.writeFile(testFilePath, combinedFileText);
        console.log("File created Succesfully");
        
      } else if (language.toLowerCase() === "python") {
        await fs.promises.writeFile(testFilePath, fileText);
      } else if (language.toLowerCase() === "go") {
        const combinedFileText = fileText + '\n' + modifiedCode;
        await fs.promises.writeFile(testFilePath, combinedFileText);
      } else if (language.toLowerCase() === "java") {
        await fs.promises.writeFile(testFilePath, modifiedCode + '\n' + fileText);
      }
      console.log("Test file written:", testFilePath);
    } catch (error) {
      console.error("Error writing test file:", error);
      return reject(error);
    }

    const outputFilePath = path.join(path.dirname(testFilePath), "gocodeo_out.txt");
    const os = require("os");
    const platform = os.platform();
    // let command = "";
    const command = getTestCommand(language, framework, testFilePath, testFileName, outputFilePath);
    // console.log(command,"commandd");
    
    try {
      runCommandSync(command);

      await waitForFile(outputFilePath, 5000);

      let sampleOutput = "";
      try {
        sampleOutput = await fs.promises.readFile(outputFilePath, "utf8");
        console.log("Test Output:", sampleOutput);
      } catch (error) {
        console.error("Error reading output file:", error);
      }

      let testResults = [];
      if (["javascript", "typescript"].includes(language.toLowerCase())) {
        // testResults = parseJSTestOutput(sampleOutput, testTitle, framework);
        if (framework.toLowerCase() === "mocha") {
          testResults = parseMochaTestOutput(sampleOutput, testTitle, testName);
        } else if (framework.toLowerCase() === "jest" || framework.toLowerCase() === "react testing library") {
          testResults = parseJSTestOutput(sampleOutput, testTitle, testName);
        }
      } else if (language.toLowerCase() === "python") {
        if (framework === "unittest") {
          console.log("Parsing unittest output...");
          testResults = parsePythonUnittestOutput(sampleOutput, testTitle, testName);
        } else if (framework === "pytest") {
          console.log("Parsing pytest output...");
          testResults = parsePythonPytestOutput(sampleOutput, testTitle, testName);
        }

      } else if (language.toLowerCase() === "go") {
        if (framework.toLowerCase() === "testify") {
          console.log("Parsing Testify output...");
          testResults = parseGoTestifyOutput(sampleOutput,testTitle,testName);
        } else if (framework.toLowerCase() === "ginkgo") {
          console.log("Parsing Ginkgo output...");
          testResults = parseGoGinkgoOutput(sampleOutput,testTitle,testName);
        }
      } else if (language.toLowerCase() === "java") {
        if (framework.toLowerCase() === "junit") {
          console.log("Parsing Testify output...");
          testResults = parseJunitTestOutput(
            sampleOutput,
            testTitle,
            testName
          );
        }
      }
      console.log(testResults, "testResultxyz");
      console.log(sampleOutput, "sampleOutputxyz");
      console.log(testTitle, "testTitlexyz");
      resolve(testResults);
    } catch (error) {
      console.error("Error during test execution:", error);
      reject(error);
    } finally {
      // Clean up: Delete the test file
      if (fs.existsSync(testFilePath)) {
        console.log("File exists:", testFilePath);
        
        try {
          fs.unlinkSync(testFilePath);
          console.log("Deleted temporary test file:", testFilePath);
        } catch (error) {
          console.error("Error deleting test file:", error);
        }
      }
    }
  });
}
 
