import * as vscode from 'vscode';
import * as path from 'path';
import * as fs from 'fs';
import ignore from 'ignore';

// Default patterns to ignore
const defaultIgnorePatterns = [
  '.git',
  'node_modules',
  'build',
  'dist',
  'coverage',
  '.DS_Store',
  '*.log',
];

// Files to exclude from initial suggestions but allow when explicitly searched
const configFilePatterns = [
  'package.json',
  'tsconfig.json',
  'webpack.config.*',
  '.env*',
  '.eslintrc.*',
  '.prettierrc.*',
  '.babelrc.*'
];

// Files to always exclude from suggestions
const excludedFilePatterns = [
  // System files
  'thumbs.db',
  '.DS_Store',
  // Lock files
  '*.lock',
  'yarn.lock',
  // Build artifacts
  '*.map',
  '*.min.*',
];

// Function to check if file is a config file
function isConfigFile(fileName: string): boolean {
  return configFilePatterns.some(pattern => {
    if (pattern.includes('*')) {
      const regex = new RegExp('^' + pattern.replace(/\*/g, '.*') + '$');
      return regex.test(fileName);
    }
    return fileName === pattern;
  });
}

// Function to check if file should be included in suggestions
function shouldIncludeFile(fileName: string, isExplicitSearch: boolean = false): boolean {
  // Check against excluded patterns first (always exclude these)
  for (const pattern of excludedFilePatterns) {
    if (pattern.includes('*')) {
      const regex = new RegExp('^' + pattern.replace(/\*/g, '.*') + '$');
      if (regex.test(fileName)) return false;
    } else if (fileName === pattern || fileName.startsWith(pattern)) {
      return false;
    }
  }

  // If it's a config file, only include if explicitly searched for that type
  if (isConfigFile(fileName)) {
    return isExplicitSearch;
  }

  // Include common code files
  const validExtensions = [
    '.ts', '.tsx', '.js', '.jsx', '.py', '.java', '.cpp', '.c',
    '.html', '.css', '.scss', '.sass', '.less', '.vue', '.php',
    '.rb', '.go', '.rs', '.swift', '.kt', '.cs', '.fs', '.clj',
    '.scala', '.dart', '.ex', '.exs', '.erl', '.ml', '.mli',
    '.json'
  ];
  
  return validExtensions.some(ext => fileName.toLowerCase().endsWith(ext));
}

// Function to check if directory should be ignored
function shouldIgnoreDirectory(dirPath: string): boolean {
  const dirName = path.basename(dirPath);
  return defaultIgnorePatterns.some(pattern => 
    pattern === dirName || 
    (pattern.startsWith('.') && dirName.startsWith('.'))
  );
}

// Optimized level-by-level directory search with relevance scoring
// 1. Modified searchFilesInDirectory:
// -------------------------------------
async function searchFilesInDirectory(
  dirPath: string,
  searchQuery: string,
  maxResults: number = 10
): Promise<Array<{ path: string; score: number }>> {
  const results: Array<{ path: string; score: number }> = [];
  const queue: Array<{ path: string; depth: number }> = [{ path: dirPath, depth: 0 }];
  const seen = new Set<string>();

  const searchQueryLower = searchQuery.toLowerCase();
  // Determine if were doing an explicit config search (if needed)
  const isConfigSearch = configFilePatterns.some(pattern =>
    pattern.toLowerCase().includes(searchQueryLower)
  );

  while (queue.length > 0) {
    const { path: currentPath, depth } = queue.shift()!;

    try {
      const entries = await fs.promises.readdir(currentPath, { withFileTypes: true });

      // Process files first
      for (const entry of entries) {
        if (entry.isFile()) {
          const fileName = entry.name;
          const fullPath = path.join(currentPath, fileName);

          if (seen.has(fullPath)) continue;
          seen.add(fullPath);

          // Only include files whose names start with the query
          const fileNameLower = fileName.toLowerCase();
          if (!fileNameLower.startsWith(searchQueryLower)) continue;

          const isConfig = isConfigFile(fileName);
          // Only include config files if explicitly searching for them
          if (!shouldIncludeFile(fileName, isConfigSearch && isConfig)) continue;

          // Now calculate a score (we have a simple 2tier system: exact match vs. startsWith)
          let score = fileNameLower === searchQueryLower ? 100 : 90;

          // Boost config files only if explicitly searching for them
          if (isConfig && isConfigSearch) {
            // Even if its not an exact match, let it score a bit lower than an exact match
            score = fileNameLower === searchQueryLower ? 100 : 85;
          }

          // Penalize config files in normal searches
          if (isConfig && !isConfigSearch) {
            score = 0;
          }

          // Adjust score based on depth (files in the search root get the highest boost)
          score = score / (depth + 1);

          if (score > 0) {
            results.push({ path: fullPath, score });
          }
        }
      }

      // Then process directories (skip ignored directories)
      for (const entry of entries) {
        if (entry.isDirectory() && !shouldIgnoreDirectory(path.join(currentPath, entry.name))) {
          queue.push({
            path: path.join(currentPath, entry.name),
            depth: depth + 1
          });
        }
      }

      // Sort results by score and stop early if we have enough
      results.sort((a, b) => b.score - a.score);
      if (results.length >= maxResults * 2) {
        queue.length = 0;
      }
    } catch (error) {
      console.error(`Error reading directory ${currentPath}:`, error);
    }
  }

  return results.slice(0, maxResults);
}


// Get all active tab files
function getAllActiveTabs(): Array<{ label: string; path: string }> {
  const openTabFiles = new Map<string, { label: string; path: string }>();

  try {
    // First try using Tab Groups API
    if (vscode.window.tabGroups) {
      vscode.window.tabGroups.all.forEach(group => {
        group.tabs.forEach(tab => {
          if (tab.input instanceof vscode.TabInputText) {
            const filePath = tab.input.uri.fsPath;
            const fileName = path.basename(filePath);

            if (filePath && 
                shouldIncludeFile(fileName) &&
                fs.existsSync(filePath)) {
              openTabFiles.set(filePath, {
                label: fileName,
                path: filePath
              });
            }
          }
        });
      });
    }

    // Fallback for older VS Code versions or if no tabs found
    if (openTabFiles.size === 0) {
      vscode.window.visibleTextEditors.forEach(editor => {
        const filePath = editor.document.uri.fsPath;
        const fileName = path.basename(filePath);

        if (filePath && 
            shouldIncludeFile(fileName) &&
            fs.existsSync(filePath)) {
          openTabFiles.set(filePath, {
            label: fileName,
            path: filePath
          });
        }
      });
    }

    return Array.from(openTabFiles.values())
      .sort((a, b) => a.label.localeCompare(b.label));

  } catch (error) {
    console.error('Error getting active tabs:', error);
    return [];
  }
}

// Get filtered file suggestions based on query
// 2. Modified getFileContextSuggestions:
// ----------------------------------------
export async function getFileContextSuggestions(query: string): Promise<Array<{ label: string; path: string }>> {
  try {
    const searchQuery = query.trim().replace("@", "").toLowerCase();

    // Get all active tab files
    const allActiveTabs = getAllActiveTabs();

    // If the query is empty, return all active tabs
    if (!searchQuery) {
      return allActiveTabs;
    }

    // Filter active tabs to only include those whose file names start with the query.
    // (This prevents showing e.g. tsconfig.app.json for query "p")
    const activeTabs = allActiveTabs.filter(file =>
      file.label.toLowerCase().startsWith(searchQuery)
    );

    const workspaceResults: Array<{ label: string; path: string; score: number; level: number }> = [];
    const activeTabPaths = new Set(activeTabs.map(tab => tab.path));

    // PRIORITY 1: If there is an active editor, use its folder and its ancestors
    const activeEditor = vscode.window.activeTextEditor;
    if (activeEditor) {
      let currentFolder = path.dirname(activeEditor.document.uri.fsPath);
      // Find the workspace folder that contains the active file
      const workspaceFolder = vscode.workspace.getWorkspaceFolder(activeEditor.document.uri);
      let level = 0;
      while (currentFolder && workspaceFolder && currentFolder.startsWith(workspaceFolder.uri.fsPath)) {
        // Search within the current folder.
        // We limit results in each folder so that current folder files (level 0) are ranked higher.
        const files = await searchFilesInDirectory(currentFolder, searchQuery, 10);
        files.forEach(({ path: filePath, score }) => {
          if (!activeTabPaths.has(filePath)) {
            workspaceResults.push({
              label: path.relative(currentFolder, filePath), // Or use basename(filePath) if preferred
              path: filePath,
              score,
              level, // Lower level means closer to the active file folder.
            });
          }
        });
        const parent = path.dirname(currentFolder);
        if (parent === currentFolder) break;
        currentFolder = parent;
        level++;
      }
    } else {
      // Fallback: search each workspace folder
      const folders = vscode.workspace.workspaceFolders;
      if (folders) {
        for (const folder of folders) {
          const files = await searchFilesInDirectory(folder.uri.fsPath, searchQuery, 10);
          files.forEach(({ path: filePath, score }) => {
            workspaceResults.push({
              label: path.relative(folder.uri.fsPath, filePath),
              path: filePath,
              score,
              level: 0,
            });
          });
        }
      }
    }

    // Sort the workspace results: first by folder proximity (level) then by score
    workspaceResults.sort((a, b) => {
      if (a.level !== b.level) {
        return a.level - b.level;
      }
      return b.score - a.score;
    });

    // Combine: active tab files first, then workspace suggestions
    const combined = [
      ...activeTabs,
      ...workspaceResults.map(({ label, path }) => ({ label, path }))
    ].slice(0, 10);
    console.log(combined, "combined_suggetions");
    
    return combined;
  } catch (error) {
    console.error('Error getting file suggestions:', error);
    return [];
  }
}


// Get file content by path
export async function getFileContent(filePath: string): Promise<string> {
  try {
    const content = await fs.promises.readFile(filePath, 'utf-8');
    return content;
  } catch (error) {
    console.error('Error reading file:', error);
    return '';
  }
}
 

/**
 * Reads the given directory and returns an array of file suggestions.
 * Adjust the filtering logic (e.g. ignore hidden files, only include certain extensions, etc.) as needed.
 */
export async function getRecursiveNearByFiles(
  directory: string,
  additionalNeeded: number,
  visitedDirs = new Set<string>()
): Promise<Array<{ label: string; path: string }>> {
  let suggestions: Array<{ label: string; path: string }> = [];

  // Avoid re-visiting directories.
  if (visitedDirs.has(directory)) {
    return suggestions;
  }
  visitedDirs.add(directory);

  try {
    const items = await fs.promises.readdir(directory);

    // First, add files from the current directory.
    for (const item of items) {
      const fullPath = path.join(directory, item);
      const stat = await fs.promises.stat(fullPath);
      if (stat.isFile() && shouldIncludeFileWith(item)) {
        suggestions.push({ label: item, path: fullPath });
        if (suggestions.length >= additionalNeeded) {
          return suggestions;
        }
      }
    }

    // If not enough files were found, iterate over subdirectories.
    for (const item of items) {
      const fullPath = path.join(directory, item);
      const stat = await fs.promises.stat(fullPath);
      if (stat.isDirectory()) {
        const subSuggestions = await getRecursiveNearByFiles(
          fullPath,
          additionalNeeded - suggestions.length,
          visitedDirs
        );
        suggestions = suggestions.concat(subSuggestions);
        if (suggestions.length >= additionalNeeded) {
          break;
        }
      }
    }

    return suggestions;
  } catch (error) {
    console.error("Error reading directory:", directory, error);
    return suggestions;
  }
}

/**
 * Example filter function.
 * Modify this to suit your needs (for instance, filtering hidden files, etc.).
 */
function shouldIncludeFileWith(fileName: string): boolean {
  return !fileName.startsWith(".");
}


