export function modifyGoCode(sourceCode: any) {
    // Combine all imports into a single string
    let imports = sourceCode.imports.join('\n');
    let code = sourceCode.main.code;

    // Remove any `package` declaration from the source code, including `package mathutils`
    code = code.replace(/^\s*package\s+\w+\s*\n/, ''); // Matches `package <identifier>` at the start of the file

    // Remove `import` statements block from the source code
    code = code.replace(/^\s*import\s+\(([\s\S]*?)\)\s*\n/, ''); // Matches `import (...)` block
    code = code.replace(/^\s*import\s+"[^"]+"\s*\n/gm, ''); // Matches single `import "..."` lines

    // Combine imports and remaining source code with proper order
    const combinedCode = `${imports}\n\n${code.trim()}`; // Trim to remove leading/trailing spaces
    return combinedCode;
}
