const crypto = require("crypto");
const algorithm = "aes-256-cbc";
// const initVector = "\xba\xf9\xf2\x929\x0e>F\x80\xc4\x86\xb3\xff\xdbj\xa7";
const initVector = "GoCodeo63ae90857";
const getSecretKey = (secretKey:string)=>{
    return secretKey + 'GoCodeo1';
}

export const encrypt = (secretKey:string, data:string) => {
    secretKey = getSecretKey(secretKey);
    const cipher = crypto.createCipheriv(algorithm, secretKey, initVector);
    let encryptedData = cipher.update(data, "utf-8", "hex");
    encryptedData += cipher.final("hex");
    return encryptedData;
}
