// Add these interfaces
interface SessionInfo {
  sessionId: string;
  dateCreated: string;
}

interface accountProfile {
  uid: string;
  extensionVersion: string
}

interface ChatHistory {
  sessionId: string;
  chatHistory: Array<{
    question: string;
    answer: string;
    timestamp: string;
    context: string[];
  }>;
  title: string;
}

interface BuildHistory {
  sessionId: string;
  buildHistory: Array<{
    input: string;
    output: string;
    timestamp: string;
  }>;
  title: string;
  rootDirectory: string;
}
