function manageSessionsFile(sessionDir: string, sessionId: string): void {
  const sessionsFile = path.join(sessionDir, "sessions.json");
  let sessions: SessionInfo[] = [];
  // console.log(sessionsFile, sessionId, "sessionsFile,sessionId");
  // Read existing sessions or create new file
  if (fs.existsSync(sessionsFile)) {
    const content = fs.readFileSync(sessionsFile, "utf8");
    sessions = JSON.parse(content);
  }

  // Add new session
  const timestamp = sessionId?.split("_")[1];
  sessions.push({
    sessionId: sessionId,
    dateCreated: timestamp,
  });
  // console.log(sessions, "sessions");

  // Write back to file
  fs.writeFileSync(sessionsFile, JSON.stringify(sessions, null, 2));
}

export function manageAccountsFile(systemInfo) {
  const userDataPath =
    process.platform === "win32"
      ? process.env.APPDATA ||
      path.join(process.env.USERPROFILE || "", "AppData", "Roaming")
      : path.join(process.env.HOME || "", ".config");

  const accountsDir = path.join(userDataPath, "gocodeo", "User", "globalStorage");
  const accountsFile = path.join(accountsDir, "storage.json")
  let associatedProfiles: accountProfile = []


  if (!fs.existsSync(accountsDir)) {
    // console.log(`2201: Directory ${accountsDir} does not exist. Creating it`);
    fs.mkdirSync(accountsDir, { recursive: true });
  }

  if (!fs.existsSync(accountsFile)) {
    associatedProfiles.push({
      uid: systemInfo?.uid,
      extensionVersion: systemInfo?.extensionVersion
    })
    const sampleJson = { deviceId: systemInfo?.deviceId, associatedProfiles: associatedProfiles };
    // console.log(`2201: File ${accountsFile} does not exist. Creating file with sample JSON. ${sampleJson}`);
    fs.writeFileSync(accountsFile, JSON.stringify(sampleJson, null, 2), "utf8");
  } else {
    const content = fs.readFileSync(accountsFile, "utf8");
    const account = JSON.parse(content);
    associatedProfiles = account?.associatedProfiles

    // console.log("2201: account file contents", account)
  }
}

export function getRecentBuildHistories(): BuildHistory[] {
  const userDataPath =
    process.platform === "win32"
      ? process.env.APPDATA ||
      path.join(process.env.USERPROFILE || "", "AppData", "Roaming")
      : path.join(process.env.HOME || "", ".config");

  const sessionDir = path.join(userDataPath, "gocodeo", "build_sessions");

  if (!fs.existsSync(sessionDir)) {
    // console.log(`Directory ${sessionDir} does not exist.`);
    return [];
  }

  const files = fs
    .readdirSync(sessionDir)
    ?.filter((file) => file.endsWith(".json") && file !== "sessions.json")
    ?.map((file) => {
      const fullPath = path.join(sessionDir, file);
      const timestamp = parseInt(file?.split("_")[1]?.split(".")[0] || "0");
      return { file, timestamp, fullPath };
    })
    .sort((a, b) => b.timestamp - a.timestamp);

  const recentHistories: BuildHistory[] = [];

  for (const file of files) {
    try {
      const content = fs.readFileSync(file.fullPath, "utf8");
      const buildData: BuildHistory = JSON.parse(content);

      if (buildData.buildHistory && buildData.buildHistory.length > 0) {
        recentHistories.push(buildData);
      } else {
        fs.unlinkSync(file.fullPath);
        console.log(
          `Deleted empty build history file: ${path.basename(file.fullPath)}`
        );
      }
    } catch (err) {
      console.error(`Error reading file ${file.file}:`, err);

      try {
        fs.unlinkSync(file.fullPath);
        console.log(
          `Deleted corrupted build history file due to read error: ${path.basename(
            file.fullPath
          )}`
        );
      } catch (deleteErr) {
        console.error(
          `Error deleting file ${path.basename(file.fullPath)}:`,
          deleteErr
        );
      }
    }
  }

  // Limit to the 10 most recent histories
  if (recentHistories.length > 21) {
    const excessFiles = files.slice(21); // Get the files exceeding the latest 20
    for (const excessFile of excessFiles) {
      try {
        fs.unlinkSync(excessFile.fullPath);
        console.log(
          `Deleted oldest build history file: ${path.basename(
            excessFile.fullPath
          )}`
        );
      } catch (deleteErr) {
        console.error(
          `Error deleting file ${path.basename(excessFile.fullPath)}:`,
          deleteErr
        );
      }
    }
  }

  console.log(
    `Found ${recentHistories.length} non-empty build histories in build_sessions`
  );
  return recentHistories.slice(0, 20);
}

export function getRecentChatHistories(): ChatHistory[] {
  // Calculate the session directory based on the operating system
  const userDataPath =
    process.platform === "win32"
      ? process.env.APPDATA ||
      path.join(process.env.USERPROFILE || "", "AppData", "Roaming")
      : path.join(process.env.HOME || "", ".config");

  const sessionDir = path.join(userDataPath, "gocodeo", "sessions");

  const files = fs
    .readdirSync(sessionDir)
    ?.filter((file) => file.endsWith(".json") && file !== "sessions.json")
    ?.map((file) => {
      const fullPath = path.join(sessionDir, file);
      const timestamp = parseInt(file?.split("_")[1]?.split(".")[0] || "0");
      return { file, timestamp, fullPath };
    })
    .sort((a, b) => b.timestamp - a.timestamp);

  const recentHistories: ChatHistory[] = [];

  for (const file of files) {
    try {
      const content = fs.readFileSync(file.fullPath, "utf8");
      const chatData: ChatHistory = JSON.parse(content);

      if (chatData.chatHistory && chatData.chatHistory.length > 0) {
        recentHistories.push(chatData);

        // Stop if we have found the required non-empty histories (either 1 or 2)
        // if (recentHistories.length >= 2) {
        //   break;
        // }
      } else {
        // Delete the file immediately if it's empty
        fs.unlinkSync(file.fullPath);
        console.log(
          `Deleted empty chat history file: ${path.basename(file.fullPath)}`
        );
      }
    } catch (err) {
      console.error(`Error reading file ${file.file}:`, err);

      // Delete the file immediately if there's an error reading it
      try {
        fs.unlinkSync(file.fullPath);
        console.log(
          `Deleted corrupted chat history file due to read error: ${path.basename(
            file.fullPath
          )}`
        );
      } catch (deleteErr) {
        console.error(
          `Error deleting file ${path.basename(file.fullPath)}:`,
          deleteErr
        );
      }
    }
  }

  // Limit to the 10 most recent histories
  if (recentHistories.length > 11) {
    const excessFiles = files.slice(11); // Get the files exceeding the latest 10
    for (const excessFile of excessFiles) {
      try {
        fs.unlinkSync(excessFile.fullPath);
        console.log(
          `Deleted oldest chat history file: ${path.basename(
            excessFile.fullPath
          )}`
        );
      } catch (deleteErr) {
        console.error(
          `Error deleting file ${path.basename(excessFile.fullPath)}:`,
          deleteErr
        );
      }
    }
  }

  console.log(`Found ${recentHistories.length} non-empty chat histories`);
  console.log("Recent histories:", recentHistories);
  return recentHistories.slice(0, 10);
}

let isAutocompleteActive = false; // shared between autocomplete and emptyline decorator to avoid overwriting

function registerEmptyLineDecorator(context: vscode.ExtensionContext) {
  const emptyLineDecorationType = vscode.window.createTextEditorDecorationType(
    {}
  );

  context.subscriptions.push(
    vscode.window.onDidChangeTextEditorSelection((event) => {
      const editor = event.textEditor;
      if (!editor) return;

      const selection = editor.selection;

      editor.setDecorations(emptyLineDecorationType, []);

      if (isAutocompleteActive) return;

      if (!selection.isEmpty) return;

      const cursorPosition = selection.start.character;
      if (cursorPosition !== 0) return;

      const line = editor.document.lineAt(selection.start.line);
      const commandKey = process.platform === "darwin" ? "Cmd" : "Ctrl";

      if (line.isEmptyOrWhitespace) {
        const decorationOptions: vscode.DecorationOptions[] = [
          {
            range: line.range,
            renderOptions: {
              after: {
                contentText: `GoCodeo-BUILD: ${commandKey}+Shift+K | GoCodeo-ASK: ${commandKey}+Shift+L`,
                color: new vscode.ThemeColor("editorLineNumber.foreground"),
                margin: "0 0 0 10px",
                fontStyle: "italic",
                opacity: "1.5",
              },
            },
          },
        ];
        editor.setDecorations(emptyLineDecorationType, decorationOptions);
      }
    })
  );

  // Clean up when editor changes
  context.subscriptions.push(
    vscode.window.onDidChangeActiveTextEditor(() => {
      if (vscode.window.activeTextEditor) {
        vscode.window.activeTextEditor.setDecorations(
          emptyLineDecorationType,
          []
        );
      }
    })
  );
}

function registerSelectionHoverProvider(context: vscode.ExtensionContext) {
  const decorationType = vscode.window.createTextEditorDecorationType({});
  let debounceTimer: NodeJS.Timeout | null = null;

  context.subscriptions.push(
    vscode.window.onDidChangeTextEditorSelection((event) => {
      if (debounceTimer) {
        clearTimeout(debounceTimer);
      }

      debounceTimer = setTimeout(() => {
        const editor = event.textEditor;
        if (!editor) return;

        const selection = editor.selection;
        if (selection.isEmpty) {
          editor.setDecorations(decorationType, []);
          return;
        }

        // Get the line number of the selection end
        const lineNumber = selection.end.line;
        const lineEndPosition = editor.document.lineAt(lineNumber).range.end;
        const decorationRange = new vscode.Range(
          lineEndPosition,
          lineEndPosition
        );
        const commandKey = process.platform === "darwin" ? "Cmd" : "Ctrl";

        const decorationOptions: vscode.DecorationOptions[] = [
          {
            range: decorationRange,
            renderOptions: {
              after: {
                contentText: `Ask GoCodeo (${commandKey}+Shift+L) | Edit (${commandKey}+Shift+E)`,
                backgroundColor: new vscode.ThemeColor(
                  "editorWidget.background"
                ),
                color: new vscode.ThemeColor("editorWidget.foreground"),
                margin: "0 20px 0 60px",
                border: "1px solid",
                borderColor: new vscode.ThemeColor("editorWidget.border"),
                borderRadius: "3px",
                padding: "2px 8px",
              },
            },
          },
        ];

        editor.setDecorations(decorationType, decorationOptions);
      }, 200);
    })
  );
}

async function getTerminalContents(commands = -1): Promise<string> {
  const tempCopyBuffer = await vscode.env.clipboard.readText();
  if (commands < 0) {
    await vscode.commands.executeCommand("workbench.action.terminal.selectAll");
  } else {
    for (let i = 0; i < commands; i++) {
      await vscode.commands.executeCommand(
        "workbench.action.terminal.selectToPreviousCommand"
      );
    }
  }
  await vscode.commands.executeCommand(
    "workbench.action.terminal.copySelection"
  );
  await vscode.commands.executeCommand(
    "workbench.action.terminal.clearSelection"
  );
  let terminalContents = (await vscode.env.clipboard.readText()).trim();
  await vscode.env.clipboard.writeText(tempCopyBuffer);

  if (tempCopyBuffer === terminalContents) {
    // This means there is no terminal open to select text from
    return "";
  }

  // Sometimes the above won't successfully separate by command, so we attempt manually
  const lines = terminalContents.split("\n");
  const lastLine = lines.pop()?.trim();
  if (lastLine) {
    let i = lines.length - 1;
    while (i >= 0 && !lines[i].trim().startsWith(lastLine)) i--;
    terminalContents = lines.slice(Math.max(i, 0)).join("\n");
  }

  return terminalContents;
}

