  let SupabaseLoginCommand = vscode.commands.registerCommand('gocodeo.supabaseLogin', async () => {
    console.log("Connecting to Supabase...");

    // Open external authentication URL
    const authUrl = `https://api.supabase.com/v1/oauth/authorize?client_id=${SUPABASE_CLIENT_ID}&redirect_uri=${SUPABASE_REDIRECT_URL}&response_type=code&scope=read`;
    vscode.env.openExternal(vscode.Uri.parse(authUrl));

    // Return a promise that will resolve once the URI handler catches the callback
    return new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        reject(new Error("Timeout: Supabase access token not received within the allowed time."));
      }, 20000); // Timeout after 20 seconds

      // Register URI handler to catch the callback
      const uriHandler = vscode.window.registerUriHandler({
        handleUri: async (uri: vscode.Uri) => {
          const params = getParams(uri.query);

          if (uri.path === '/api/v1/supabase-auth-callback' && params.supabase_access_token) {
            // Set the tokens in localStorage
            localStore.setValue("gocodeo.supabaseAccessToken", params.supabase_access_token);
            localStore.setValue("gocodeo.supabaseRefreshToken", params.supabase_refresh_token);

            // Resolve the promise with the token
            clearTimeout(timeout); // Clear the timeout once the token is received
            resolve(params.supabase_access_token);

            vscode.window.showInformationMessage("Successfully authorized Supabase.");
          }
        },
      });

      // Cleanup after the URI handler is no longer needed (in case of timeout or completion)
      setTimeout(() => {
        uriHandler.dispose();
      }, 20000);
    });
  });

  // Function to parse query parameters
  function getParams(query: string) {
    const params = {};
    query
      .replace(/^\?/, "")
      .split("&")
      .forEach((e) => {
        if (e) {
          var r = e.split("="),
            i = r[0],
            n = r[1];
          params[decodeURIComponent(i)] = decodeURIComponent(n);
        }
      });
    return params;
  }


