// Helper: check if a document is probably binary
function isBinaryDocument(document: vscode.TextDocument): boolean {
  // You might check the languageId if you set it for binary files in your extension.
  // Alternatively, a simple heuristic is to check for null characters.
  const text = document.getText();
  return text.includes('\0');
}
