  function getCodeWithCursorMarker(
    document: TextDocument,
    cursorPosition: Position
  ): string {
    try {
      const cursorOffset = document.offsetAt(cursorPosition);
      const fullCode = document.getText();
      // Insert the <MID> identifier at the cursor position
      const codeWithMid = `${fullCode.slice(
        0,
        cursorOffset
      )}<MID>${fullCode.slice(cursorOffset)}`;
      // console.log(
      //   `\n=== Code with <MID> identifier ===\n${codeWithMid}\n=== End of code ===\n`
      // );
      // console.log(`Total characters: ${codeWithMid.length}`);
      return codeWithMid;
    } catch (error) {
      console.error("Error adding <MID> identifier to code:", error);
      return document.getText();
    }
  }

  function getCodeUntilCursor(
    document: TextDocument,
    cursorPosition: Position
  ): string {
    try {
      const range = new vscode.Range(new Position(0, 0), cursorPosition);
      const code = document.getText(range);

      // console.log("\n=== Code until cursor ===");
      // console.log(code);
      // console.log("\n=== End of code ===");
      // console.log(`Total characters: ${code.length}`);

      return code;
    } catch (error) {
      console.error("Error getting code until cursor:", error);
      return "";
    }
  }

  // Add this interface for the debounced function
  interface DebouncedFunction<T> {
    (document: vscode.TextDocument, position: vscode.Position): Promise<T>;
    cancel(): void;
  }

  // Add this helper function to get the last word before cursor
  function getLastWord(document: TextDocument, position: Position): string {
    const line = document.lineAt(position.line).text;
    const linePrefix = line.substring(0, position.character);
    const words = linePrefix.split(/[\s(){}[\],.:;=+\-*/&|<>~]+/);
    return words[words.length - 1];
  }


