//@ts-ignore
import * as vscode from "vscode";
import * as os from 'os';
import * as crypto from 'crypto';

import { MIXPANEL_KEY } from './Constants';
const MixPanel = require('mixpanel');

export class Analytics {
    private systemInfo: any;
    private ctx: vscode.ExtensionContext;
    constructor(ctx: vscode.ExtensionContext){
        this.ctx = ctx;
        //@ts-ignore
        this.conn = MixPanel.init(MIXPANEL_KEY, {
            // keepAlive: false,
            debug: true,
        });
        this.systemInfo = getSystemDetails(ctx);
    }
    public track(eventName:string, extraData:any){
        console.log("TRACKING EVENT", eventName, "EXTRA DATA", extraData, "DISTINCT UID", extraData.userTrackPoints.distinct_id)
        if(extraData.uid)
            extraData.distinct_id = extraData.uid;
        else if (extraData?.userTrackPoints?.distinct_id)
            extraData.distinct_id = extraData?.userTrackPoints?.distinct_id;
        if (extraData.user_ip_address)
            // extraData.ip_address = extraData.user_ip_address
            extraData.ip = extraData.user_ip_address
            // extraData.$ip = extraData.user_ip_address
        extraData = {...this.systemInfo, ...extraData};
        console.log("FINAL DATA FOR TRACKING", extraData)
        //@ts-ignore
        this.identify(extraData);
        //@ts-ignore
        this.conn.track(eventName, extraData);
    }
    public identify(user:any, isNew:boolean) {
    // create or update a user in Mixpanel Engage
    // MPObject.identify(user.uid || user.email);
    // const extraData = {...this.systemInfo, ...user};
    const uid = user.distinct_id || user.uid || user.email;
    const userProfile = {    
        distinct_id: uid,    
        $email: user.email,
        $name: user.name || user.username,
        $created: new Date(user.createdAt || 0).toISOString(),
        osPlatform: user.osPlatform || process.platform,
        editorVersion: user.editorVersion,
        extensionVersion: user.extensionVersion,
        ip: user.ip || user.user_ip_address,
    };

    console.log("UPDATING USER PROFILE", userProfile);
        //@ts-ignore
        this.conn.people.set(uid, userProfile);
    // if (isNew) {
    //     MPObject.alias('distinct_id', user.uid);
    // }
    }
}

export const getUniqueDeviceId = (): string => {
    const networkInterfaces = os.networkInterfaces();
    //@ts-ignore
    const macAddresses = Object.values(networkInterfaces)
        .flat()//@ts-ignore
        .filter((iface) => !iface.internal && iface.mac !== '00:00:00:00:00:00')//@ts-ignore
        .map((iface) => iface.mac);

    const macAddress = macAddresses.length > 0 ? macAddresses[0] : 'unknown';
    const hash = crypto.createHash('sha256');
    hash.update(macAddress);
    return hash.digest('hex');
};

export const getSystemDetails = (ctx: vscode.ExtensionContext)=> {
    const osMap: Record<string, string> = {
        win32: "Windows",
        darwin: "Mac OS X",
        linux: "Linux",
        android: "Android",
        ios: "iOS"
      };
    const detectedOS = osMap[process.platform] || "Unknown";
    return {
        vscodeExtensionMode: ctx.extensionMode,
        extensionId: ctx.extension.id,
        extensionPath: ctx.extension.extensionPath,
        // extensionIsActive: ctx.extension.isActive,
        editorVersion: vscode.version,
        extensionVersion: ctx.extension.packageJSON.version,
        osPlatform: process.platform,
        $os: detectedOS,
        osVersion: process.version,
        deviceId: getUniqueDeviceId()
    };
}


export const getUserTrackPoints = (user:any, testData: any)=> {
    let trackDetails = {};
    if (user){
        trackDetails = {
            email: user.email,
            name: user.name || user.username,
            distinct_id: user.uid,
            createdAt: user.createdAt,
        }
    }
    if (testData){
        //@ts-ignore
        trackDetails.language = testData.language;
        //@ts-ignore
        trackDetails.framework = testData.framework;
        //@ts-ignore
        trackDetails.requestId = testData.requestId;
    }
    return trackDetails;
}
