import OpenAI from 'openai';
import { OpenAIStream } from './charStream';
import { constructAutocompletePrompt } from './promptBuilder';
import * as dotenv from 'dotenv';
dotenv.config();

 

export async function getOpenAICompletion(
  filepath: string,
  cursorLine: number,
  fullPrefix: string,
  fullSuffix: string,
  clipboardText: string,
  languageInfo: any,
  options: any,
  recentlyEditedRanges: any,
  signal?: AbortSignal
): Promise<string> {
  try {
    const openai = new OpenAI({ apiKey: process.env.REACT_APP_OPENAI_API_KEY || "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" });

    const { prefix, suffix, completeMultiline } = await constructAutocompletePrompt(
      filepath,
      cursorLine,
      fullPrefix,
      fullSuffix,
      clipboardText,
      languageInfo,
      options,
      recentlyEditedRanges
    );
    console.log(languageInfo, "languageInfo");

    // Choose system message based on language
    const SYSTEM_MSG =
      languageInfo === "python"
        ? `
        You are a Python code assistant specialized in providing context-aware, well-formatted Python code completions. 
        You are provided with a Python file containing a placeholder '{{FILL_HERE}}'. 
        Your task is to fill that placeholder with correct Python code following PEP8 guidelines.
        Return the completion inside <COMPLETION> tags. Do not include any extra commentary.
        `
                : `
        You are a HOLE FILLER. You are provided with a file containing holes, formatted as '{{HOLE_NAME}}'. Your TASK is to complete with a string to replace this hole with, inside a <COMPLETION/> XML tag, including context-aware indentation, if needed.  All completions MUST be truthful, accurate, well-written and correct.
        Always return well-formatted and properly indented code.
        Ensure function calls and definitions are structured correctly in multiple lines.
        - Always format your responses with proper indentation and spacing.
        - Any statement following a closing brace \`}\` should start on a new line.
        - Do **not** place statements immediately after \`}\` and \`;\`.
        - Make sure give a nextline after every statement (***IMPORTANT***).

        For example, instead of:

        
        <COMPLETION>function hello(){console.log("hello")} hello()</COMPLETION>
        

        Return:

        
        <COMPLETION>function hello() {
            console.log("hello");
        }

        hello();</COMPLETION>
        

        Do **NOT** return minified, single-line code unless explicitly required.
        Always maintain correct spacing and indentation.

        ## EXAMPLE QUERY:
        
        <QUERY>
        function sum_evens(lim) {
          var sum = 0;
          for (var i = 0; i < lim; ++i) {
            {{FILL_HERE}}
          }
          return sum;
        }
        </QUERY>
        
        TASK: Fill the {{FILL_HERE}} hole.
        
        ## CORRECT COMPLETION
        
        <COMPLETION>if (i % 2 === 0) {
              sum += i;
            }</COMPLETION>
        
        ## EXAMPLE QUERY:
        
        <QUERY>
        def sum_list(lst):
          total = 0
          for x in lst:
          {{FILL_HERE}}
          return total
        
        print sum_list([1, 2, 3])
        </QUERY>
        
        ## CORRECT COMPLETION:
        
        <COMPLETION>  total += x</COMPLETION>
        
        ## EXAMPLE QUERY:
        
        <QUERY>
        // data Tree a = Node (Tree a) (Tree a) | Leaf a
        
        // sum :: Tree Int -> Int
        // sum (Node lft rgt) = sum lft + sum rgt
        // sum (Leaf val)     = val
        
        // convert to TypeScript:
        {{FILL_HERE}}
        </QUERY>
        
        ## CORRECT COMPLETION:
        
        <COMPLETION>type Tree<T>
          = {$:"Node", lft: Tree<T>, rgt: Tree<T>}
          | {$:"Leaf", val: T};
        
        function sum(tree: Tree<number>): number {
          switch (tree.$) {
            case "Node":
              return sum(tree.lft) + sum(tree.rgt);
            case "Leaf":
              return tree.val;
          }
        }</COMPLETION>
        
        ## EXAMPLE QUERY:
        
        The 5th {{FILL_HERE}} is Jupiter.
        
        ## CORRECT COMPLETION:
        
        <COMPLETION>planet from the Sun</COMPLETION>
        
        ## EXAMPLE QUERY:
        
        function hypothenuse(a, b) {
          return Math.sqrt({{FILL_HERE}}b ** 2);
        }
        
        ## CORRECT COMPLETION:
        
        <COMPLETION>a ** 2 + </COMPLETION>`;

    const fullPrompt = `
    <QUERY>
    ${prefix}{{FILL_HERE}}${suffix}
    </QUERY>
    TASK: Fill the {{FILL_HERE}} hole with properly formatted code.
    ${languageInfo === "python" ? "Ensure the code is valid Python and follows PEP8 formatting." : "Ensure all statements after '}' appear on a new line."}
    `;

    const response = await openai.chat.completions.create({
      model: languageInfo === "python" ? "gpt-4o-mini" : "gpt-4o-mini", // you can choose different models if needed
      messages: [
        {
          role: "system",
          content: SYSTEM_MSG,
        },
        {
          role: "user",
          content: fullPrompt,
        },
      ],
      max_tokens: 100,
      temperature: 0.3,
      presence_penalty: 0,
      frequency_penalty: 0,
      stream: true,
      stop: ["</COMPLETION>"],
    }, { signal });

    const stream = OpenAIStream(response);
    let completion = '';

    for await (const chunk of stream) {
      completion += chunk;
    }

    completion = completion.replace(/<\/?COMPLETION>/g, '').trim();
    console.log(completion, "complete the response");

    return completion;
  } catch (error: any) {
    if (error.name === 'AbortError') {
      throw error;
    }
    throw new Error(`OpenAI API error: ${error.message}`);
  }
}

 
