import * as vscode from "vscode";
import { diff_match_patch } from "diff-match-patch";
import * as path from "path";
import * as os from "os";
import * as fs from "fs";

interface File {
  filename: string;
  content: string;
}

interface Folder {
  folder: string;
  files: File[];
}

interface ProjectStructure {
  project_directory: string;
  structure: Folder[];
}
// Function to create decorations for added/removed lines
const fileDecorationState = new Map<
  string,
  {
    addedDecoration: vscode.TextEditorDecorationType;
    removedDecoration: vscode.TextEditorDecorationType;
    originalContent: string;
    removedRanges: vscode.Range[];
  }
>();

function createDecorations() {
  const addedLineDecoration = vscode.window.createTextEditorDecorationType({
    backgroundColor: "rgba(0, 255, 0, 0.2)",
    isWholeLine: true,
  });

  const removedLineDecoration = vscode.window.createTextEditorDecorationType({
    backgroundColor: "rgba(255, 0, 0, 0.2)",
    isWholeLine: true,
  });

  return { addedLineDecoration, removedLineDecoration };
}
const activeDiffViews = new Map<
  string,
  {
    originalUri: vscode.Uri;
    editableUri: vscode.Uri;
    diffUri: vscode.Uri;
    fileName: string;
  }
>();

async function updateDecorations(
  editor: vscode.TextEditor,
  originalContent: string,
  newContent: string
) {
  try {
    const fileUri = editor.document.uri.toString();

    if (!fileDecorationState.has(fileUri)) {
      const { addedLineDecoration, removedLineDecoration } =
        createDecorations();
      fileDecorationState.set(fileUri, {
        addedDecoration: addedLineDecoration,
        removedDecoration: removedLineDecoration,
        originalContent: originalContent || "",
        removedRanges: [],
      });
    }

    const state = fileDecorationState.get(fileUri)!;

    // Clear previous ranges
    state.removedRanges = [];

    // For initial creation, mark all lines as added
    if (!state.originalContent.trim()) {
      const addedRanges: vscode.Range[] = [];
      const lines = newContent.split("\n");

      lines.forEach((line, index) => {
        if (line.trim()) {
          const range = new vscode.Range(
            new vscode.Position(index, 0),
            new vscode.Position(index, line.length)
          );
          addedRanges.push(range);
        }
      });

      editor.setDecorations(state.addedDecoration, addedRanges);
      editor.setDecorations(state.removedDecoration, []);
    }
  } catch (error) {
    console.error("Error updating decorations:", error);
  }
}

// Add these helper functions for diff handling
async function deleteCodeFiles() {
  try {
    const isWindows = process.platform === "win32";
    const possiblePaths = isWindows
      ? [
          path.join(
            process.env.LOCALAPPDATA || "",
            "Programs",
            "cursor",
            "resources",
            "app",
            "bin",
            "code.cmd"
          ),
          path.join(
            process.env.LOCALAPPDATA || "",
            "Programs",
            "cursor",
            "resources",
            "app",
            "bin",
            "code"
          ),
        ]
      : ["/usr/bin/code", "/usr/local/bin/code"];

    for (const filePath of possiblePaths) {
      if (fs.existsSync(filePath)) {
        await fs.promises.unlink(filePath);
        console.log(`Deleted file: ${filePath}`);
      }
    }
  } catch (error) {
    console.error("Error deleting code files:", error);
  }
}

let globalDiffCommandDisposable: vscode.Disposable | undefined;

async function registerDiffCommand(
  document: vscode.TextDocument
): Promise<vscode.Disposable | undefined> {
  try {
    // Dispose of existing command registration if it exists
    if (globalDiffCommandDisposable) {
      globalDiffCommandDisposable.dispose();
    }

    // Register the command
    globalDiffCommandDisposable = vscode.commands.registerCommand(
      "workbench.action.compareEditor.acceptChanges",
      async () => {
        try {
          // Save the document when changes are accepted
          if (document.isDirty) {
            await document.save();
          }
          // Cleanup
          if ((global as any).__diffCleanup) {
            (global as any).__diffCleanup();
          }
        } catch (error) {
          console.error("Error in diff command handler:", error);
        }
      }
    );

    return globalDiffCommandDisposable;
  } catch (error) {
    console.error("Error registering diff command:", error);
    return undefined;
  }
}

// Add this class before the showDiffForFile function
class DiffReadOnlyContentProvider
  implements vscode.TextDocumentContentProvider
{
  private _onDidChange = new vscode.EventEmitter<vscode.Uri>();
  private _content = new Map<string, string>();

  onDidChange = this._onDidChange.event;

  provideTextDocumentContent(uri: vscode.Uri): string {
    return this._content.get(uri.toString()) || "";
  }

  updateContent(uri: vscode.Uri, content: string): void {
    this._content.set(uri.toString(), content);
    this._onDidChange.fire(uri);
  }

  dispose() {
    this._onDidChange.dispose();
    this._content.clear();
  }
}

async function showDiffForFile(
  originalUri: vscode.Uri,
  oldContent: string,
  newContent: string,
  originalFileName: string
) {
  try {
    console.log("Starting diff process for:", originalFileName);

    // Create read-only content provider
    const contentProvider = new DiffReadOnlyContentProvider();
    const providerRegistration =
      vscode.workspace.registerTextDocumentContentProvider(
        "diff-readonly",
        contentProvider
      );

    // Create URIs for read-only content
    const leftUri = vscode.Uri.parse(
      `diff-readonly://left/${originalFileName}`
    );
    const rightUri = vscode.Uri.parse(
      `diff-readonly://right/${originalFileName}`
    );

    // Update content in the provider
    contentProvider.updateContent(leftUri, oldContent);
    contentProvider.updateContent(rightUri, newContent);

    // Create or get the editable document
    const editableUri = originalUri;
    const editableDocument = await vscode.workspace.openTextDocument(
      editableUri
    );

    // Apply new content to editable document
    const edit = new vscode.WorkspaceEdit();
    edit.replace(
      editableUri,
      new vscode.Range(0, 0, editableDocument.lineCount, 0),
      newContent
    );
    await vscode.workspace.applyEdit(edit);
    await editableDocument.save();
    await vscode.commands.executeCommand("workbench.action.closeActiveEditor");

    // Store the diff view information
    const diffKey = originalUri.toString();
    activeDiffViews.set(diffKey, {
      originalUri: originalUri,
      editableUri: editableUri,
      diffUri: leftUri,
      fileName: originalFileName,
    });
    console.log("[Diff View] Added new diff view:", diffKey);

    // Create a document for the new content without saving
    const document = await vscode.workspace.openTextDocument(originalUri);

    // Register the command for this diff session
    const diffCommandsDisposable = await registerDiffCommand(document);

    // Create a promise that will be resolved when changes are accepted
    const diffPromise = new Promise<void>((resolve) => {
      const disposables: vscode.Disposable[] = [];

      // Listen for save events
      disposables.push(
        vscode.workspace.onDidSaveTextDocument(async (savedDoc) => {
          if (savedDoc.uri.toString() === editableUri.toString()) {
            // Cleanup and resolve
            disposables.forEach((d) => d.dispose());
            resolve();
          }
        })
      );

      // Store the cleanup function globally
      (global as any).__diffCleanup = () => {
        disposables.forEach((d) => d.dispose());
        resolve();
      };
    });

    // Show diff in read-only mode
    const title = originalFileName;
    const diffOptions = {
      viewColumn: vscode.ViewColumn.Active,
      preview: false,
      preserveFocus: false,
      options: {
        readOnly: true,
        contextmenu: false,
        hover: false,
        quickSuggestions: false,
        suggestOnTriggerCharacters: false,
        parameterHints: false,
        codeLens: false,
        lightbulb: { enabled: false },
      },
    };

    // Register event listeners for the diff editor
    const editorChangeDisposable = vscode.window.onDidChangeActiveTextEditor(
      async (editor) => {
        if (editor) {
          const currentUri = editor.document.uri.toString();
          const isDiffView =
            currentUri.startsWith("diff-readonly://") ||
            Array.from(activeDiffViews.values()).some(
              (view) => view.diffUri.toString() === currentUri
            );

          if (isDiffView) {
            // For diff views, ensure they are read-only
            editor.options = {
              ...editor.options,
              readOnly: true,
              cursorStyle: vscode.TextEditorCursorStyle.Line,
              lineNumbers: "on",
              renderWhitespace: "none",
              matchBrackets: "never",
              renderControlCharacters: false,
              renderIndentGuides: false,
              selectionHighlight: false,
              occurrencesHighlight: false,
            };
          }
        }
      }
    );

    await vscode.commands.executeCommand(
      "vscode.diff",
      leftUri, // Left side (old content)
      rightUri, // Right side (new content)
      title,
      diffOptions
    );

    // Wait for changes to be accepted
    await diffPromise;

    // Cleanup
    if (diffCommandsDisposable) {
      diffCommandsDisposable.dispose();
      if (globalDiffCommandDisposable === diffCommandsDisposable) {
        globalDiffCommandDisposable = undefined;
      }
    }

    editorChangeDisposable.dispose();
    providerRegistration.dispose();
    contentProvider.dispose();

    const closeListener = vscode.window.onDidChangeVisibleTextEditors(
      (editors) => {
        const visibleUris = editors.map((e) => e.document.uri.toString());
        if (
          !visibleUris.includes(leftUri.toString()) &&
          !visibleUris.includes(rightUri.toString())
        ) {
          console.log("[Diff View] Cleaning up diff view:", diffKey);
          activeDiffViews.delete(diffKey);
          closeListener.dispose();
        }
      }
    );

    return { tempUri: leftUri };
  } catch (error) {
    console.error("Error showing diff:", error);
    throw error;
  }
}

// Add this at the top with other global variables
const changeDisposables = new Map<string, vscode.Disposable>();

// Add this function to reapply decorations when switching tabs
async function reapplyDecorations(editor: vscode.TextEditor) {
  const fileKey = editor.document.uri.toString();
  const state = fileDecorationState.get(fileKey);
  if (state) {
    const currentContent = editor.document.getText();
    await updateDecorations(editor, state.originalContent, currentContent);
  }
}

async function createFile(
  workspaceUri: vscode.Uri,
  fullPath: string,
  file: File,
  unsavedContent: string
) {
  try {
    const fullFolderPath = vscode.Uri.joinPath(workspaceUri, fullPath);
    await vscode.workspace.fs.createDirectory(fullFolderPath);

    const fileUri = vscode.Uri.joinPath(fullFolderPath, file.filename);
    const fileKey = fileUri.toString();

    let originalContent = "";
    let tempUri: vscode.Uri | undefined;

    try {
      const existingContent = await vscode.workspace.fs.readFile(fileUri);
      originalContent = new TextDecoder().decode(existingContent);
    } catch (e) {
      const initialContent = new TextEncoder().encode("");
      await vscode.workspace.fs.writeFile(fileUri, initialContent);
    }

    const document = await vscode.workspace.openTextDocument(fileUri);
    const editor = await vscode.window.showTextDocument(document, {
      preview: false,
      viewColumn: vscode.ViewColumn.One,
    });

    if (file.content) {
      if (!originalContent.trim()) {
        // First-time content handling with green decorations
        const edit = new vscode.WorkspaceEdit();
        edit.replace(
          fileUri,
          new vscode.Range(0, 0, document.lineCount, 0),
          file.content
        );
        await vscode.workspace.applyEdit(edit);

        const { addedLineDecoration } = createDecorations();
        fileDecorationState.set(fileKey, {
          addedDecoration: addedLineDecoration,
          removedDecoration: vscode.window.createTextEditorDecorationType({}),
          originalContent,
          removedRanges: [],
        });

        await updateDecorations(editor, originalContent, file.content);

        // Add save handler for first-time content
        const firstTimeSaveDisposable = vscode.workspace.onDidSaveTextDocument(
          async (savedDoc) => {
            if (savedDoc.uri.toString() === fileKey) {
              try {
                // Clean up decorations only for this specific file
                const state = fileDecorationState.get(fileKey);
                if (state) {
                  state.addedDecoration.dispose();
                  state.removedDecoration.dispose();
                  fileDecorationState.delete(fileKey);
                }

                // Dispose of event listeners for this file
                firstTimeSaveDisposable.dispose();

                // Only dispose the change disposable if it's for this file
                if (changeDisposables.has(fileKey)) {
                  changeDisposables.get(fileKey)?.dispose();
                  changeDisposables.delete(fileKey);
                }
              } catch (error) {
                console.error("Error handling first-time save cleanup:", error);
              }
            }
          }
        );

        // Add editor change handler to reapply decorations
        const editorChangeDisposable =
          vscode.window.onDidChangeActiveTextEditor(async (newEditor) => {
            if (newEditor && newEditor.document.uri.toString() === fileKey) {
              await reapplyDecorations(newEditor);
            }
          });

        // Store change disposable for this file
        const changeDisposable = vscode.workspace.onDidChangeTextDocument(
          async (e) => {
            if (e.document === document && !originalContent.trim()) {
              const currentContent = document.getText();
              const state = fileDecorationState.get(fileKey);
              if (state) {
                await updateDecorations(
                  editor,
                  state.originalContent,
                  currentContent
                );
              }
            }
          }
        );

        // Store both disposables in the map
        changeDisposables.set(
          fileKey,
          vscode.Disposable.from(changeDisposable, editorChangeDisposable)
        );
      } else {
        // Show diff with old content in temp file and new content in original file
        const { tempUri } = await showDiffForFile(
          fileUri,
          originalContent,
          file.content,
          file.filename
        );

        // Handle save cleanup
        const saveDisposable = vscode.workspace.onDidSaveTextDocument(
          async (savedDoc) => {
            if (savedDoc.uri.toString() === fileUri.toString()) {
              try {
                // Clean up decorations only for this specific file
                const state = fileDecorationState.get(fileKey);
                if (state) {
                  state.addedDecoration.dispose();
                  state.removedDecoration.dispose();
                  fileDecorationState.delete(fileKey);
                }

                // await new Promise(resolve => setTimeout(resolve, 100));
                await vscode.commands.executeCommand(
                  "workbench.action.closeActiveEditor"
                );

                await new Promise(resolve => setTimeout(resolve, 130));
                if (tempUri) {
                  try {
                    await vscode.workspace.fs.delete(tempUri);
                  } catch (error) {
                    console.error("Error deleting temp file:", error);
                  }
                }

                await new Promise(resolve => setTimeout(resolve, 50));
                const savedDocument = await vscode.workspace.openTextDocument(
                  fileUri
                );
                await vscode.window.showTextDocument(savedDocument, {
                  preview: false,
                  viewColumn: vscode.ViewColumn.One,
                });

                // Dispose of event listeners for this file
                saveDisposable.dispose();
                if (changeDisposables.has(fileKey)) {
                  changeDisposables.get(fileKey)?.dispose();
                  changeDisposables.delete(fileKey);
                }
              } catch (error) {
                console.error("Error handling save cleanup:", error);
              }
            }
          }
        );

        // Add editor change handler to reapply decorations
        const editorChangeDisposable =
          vscode.window.onDidChangeActiveTextEditor(async (newEditor) => {
            if (newEditor && newEditor.document.uri.toString() === fileKey) {
              await reapplyDecorations(newEditor);
            }
          });

        // Store change disposable for this file
        const changeDisposable = vscode.workspace.onDidChangeTextDocument(
          async (e) => {
            if (e.document === document && !originalContent.trim()) {
              const currentContent = document.getText();
              const state = fileDecorationState.get(fileKey);
              if (state) {
                await updateDecorations(
                  editor,
                  state.originalContent,
                  currentContent
                );
              }
            }
          }
        );

        // Store both disposables in the map
        changeDisposables.set(
          fileKey,
          vscode.Disposable.from(changeDisposable, editorChangeDisposable)
        );
      }
    }
  } catch (error) {
    console.error(`Error creating file ${file.filename}:`, error);
    vscode.window.showErrorMessage(
      `Failed to create file ${file.filename}: ${error.message}`
    );
  }
}

// Function to handle single file creation from streaming response with unsaved content
export async function createSingleFile(
  filePath: string,
  content: string,
  unsavedContent: string
) {
  try {
    const workspaceUri = vscode.workspace.workspaceFolders?.[0].uri;
    if (!workspaceUri) {
      vscode.window.showErrorMessage("No workspace is open.");
      return;
    }

    // Split the file path into directory and filename
    const pathParts = filePath.split("/");
    const filename = pathParts.pop() || "";
    const fullPath = pathParts.join("/");

    console.log("Created file's path", fullPath);

    // Ensure content is not undefined
    const fileContent = content || ""; // Default to empty if content is undefined

    // Create the file with initial content and then add unsaved content
    await createFile(
      workspaceUri,
      fullPath,
      {
        filename,
        content: fileContent,
      },
      unsavedContent
    );
  } catch (error) {
    console.error("Error creating file:", error);
    vscode.window.showErrorMessage("Failed to create file: " + error.message);
  }
}

// Original function for handling complete project structure, with file creation and unsaved content
export async function parseAndCreateFiles(
  apiResponse: ProjectStructure,
  unsavedContent: string
) {
  try {
    // Validate the response structure
    if (
      !apiResponse.project_directory ||
      !Array.isArray(apiResponse.structure)
    ) {
      throw new Error("Invalid response format");
    }

    const workspaceUri = vscode.workspace.workspaceFolders?.[0].uri;
    if (!workspaceUri) {
      vscode.window.showErrorMessage("No workspace is open.");
      return;
    }

    // Create project root directory
    const rootProjectUri = vscode.Uri.joinPath(
      workspaceUri,
      apiResponse.project_directory
    );
    await vscode.workspace.fs.createDirectory(rootProjectUri);

    // Create each file in the structure with unsaved content
    for (const folder of apiResponse.structure) {
      for (const file of folder.files) {
        const fullPath = `${apiResponse.project_directory}/${folder.folder}`;
        await createFile(workspaceUri, fullPath, file, unsavedContent);
      }
    }

    vscode.window.showInformationMessage(
      `Project ${apiResponse.project_directory} created successfully.`
    );
  } catch (error) {
    console.error("Error handling API response:", error);
    vscode.window.showErrorMessage(
      "Failed to process API response: " + error.message
    );
  }
}

// Add cleanup on extension deactivation
export function deactivate() {
  // Clean up all decorations and disposables
  for (const [key, state] of fileDecorationState) {
    state.addedDecoration.dispose();
    state.removedDecoration.dispose();
  }
  fileDecorationState.clear();

  // Clean up all change disposables
  for (const disposable of changeDisposables.values()) {
    disposable.dispose();
  }
  changeDisposables.clear();

  if (globalDiffCommandDisposable) {
    globalDiffCommandDisposable.dispose();
  }
}

async function deleteFolder(rootDir) {
  const folderUri = vscode.Uri.joinPath(
    vscode.workspace.workspaceFolders[0].uri,
    rootDir
  );

  try {
    const folderPath = folderUri.fsPath;

    // Close all open tabs that belong to the folder
    const closeTabsInFolder = async () => {
      const tabsToClose = [];
      const unsavedTabs = [];

      vscode.window.tabGroups.all.forEach((group) => {
        group.tabs.forEach((tab) => {
          if (
            tab.input &&
            tab.input.uri &&
            tab.input.uri.fsPath.startsWith(folderPath)
          ) {
            if (tab.isDirty) {
              unsavedTabs.push(tab);
            } else {
              tabsToClose.push(tab);
            }
          }
        });
      });

      // Save all unsaved files automatically
      for (const tab of unsavedTabs) {
        const document = await vscode.workspace.openTextDocument(tab.input.uri);
        await document.save();
      }
      for (const tab of tabsToClose) {
        const document = await vscode.workspace.openTextDocument(tab.input.uri);
        await document.save();
      }

      console.log("sxsxtabsToClose", tabsToClose);
      console.log("sxsxunsavedTabs", unsavedTabs);

      // Close all tabs
      for (const tab of [...tabsToClose, ...unsavedTabs]) {
        await vscode.window.tabGroups.close(tab);
      }
    };

    await closeTabsInFolder(); // Wait for tabs to close

    // Now delete the folder and its contents
    await vscode.workspace.fs.delete(folderUri, {
      recursive: true,
      force: true,
    });

    console.log(`Deleted folder and all contents: ${rootDir}`);
  } catch (error) {
    console.error(`Error deleting folder: ${rootDir}`, error);
    vscode.window.showErrorMessage(`Failed to delete folder: ${rootDir}`);
  }
}
export async function revertChangesOrDelete(
  filePaths: string[],
  rootDir: string | null
) {
  try {
    const workspaceUri = vscode.workspace.workspaceFolders?.[0]?.uri;
    if (!workspaceUri) {
      throw new Error("No workspace is open.");
    }
    for (const filePath of filePaths) {
      const fileUri = vscode.Uri.joinPath(workspaceUri, filePath);
      try {
        // First, try to get the actual file document
        const document = await vscode.workspace.openTextDocument(fileUri);
        console.log(`document.isDirty: ${document.isDirty}`);
        if (document.isDirty) {
          // Check if the file is now empty
          const updatedContent = (await vscode.workspace.fs.readFile(fileUri))
            .toString()
            .trim();
          if (!updatedContent) {
            console.log(`File is empty, deleting: ${filePath}`);
            // Find all tabs for this file
            const tabsToClose = [];
            console.log(`Processing unsaved content for: ${filePath}`);
            // Show the file in Column One
            await vscode.window.showTextDocument(document, {
              preview: true,
              viewColumn: vscode.ViewColumn.One,
            });
            // Retrieve saved content (on disk)
            const savedContent = (
              await vscode.workspace.fs.readFile(fileUri)
            ).toString();
            console.log("Saved Content:\n", savedContent);
            // Retrieve unsaved content (in editor)
            const unsavedContent = document.getText();
            console.log("Unsaved Content:\n", unsavedContent);
            if (unsavedContent !== savedContent) {
              const edit = new vscode.WorkspaceEdit();
              const entireRange = new vscode.Range(
                0,
                0,
                document.lineCount,
                document.getText().length
              );
              edit.replace(fileUri, entireRange, savedContent);
              await vscode.workspace.applyEdit(edit);
              await document.save();
              console.log(`Unsaved content reverted for: ${filePath}`);
            } else {
              console.log(`No unsaved content detected for: ${filePath}`);
            }
          }
          //  ------------------
          if (!updatedContent) {
            console.log(`File is empty, deleting: ${filePath}`);
            console.log(`File deleted: ${filePath}`);
            // Close the file tab in VS Code
            const tabsToClose = [];
            vscode.window.tabGroups.all.forEach((group) => {
              group.tabs.forEach((tab) => {
                if (
                  tab.input &&
                  tab.input.uri &&
                  tab.input.uri.fsPath === fileUri.fsPath
                ) {
                  tabsToClose.push(tab);
                }
              });
            });
            for (const tab of tabsToClose) {
              await vscode.window.tabGroups.close(tab);
            }
            await vscode.workspace.fs.delete(fileUri);
            console.log(`Closed tab for deleted file: ${filePath}`);
          }
          if (rootDir) {
            await deleteFolder(rootDir);
          }
        }
        else {
          // Handle non-dirty files
          console.log(`Processing non-dirty file: ${filePath}`);
          const fileUriString = fileUri.toString();
          const diffView = activeDiffViews.get(fileUriString);
          if (diffView) {
            console.log("[Revert] Found active diff view for:", filePath);
            try {
              // Get the old content from the diff-readonly URI
              const oldContent = await vscode.workspace
                .openTextDocument(diffView.diffUri)
                .then((doc) => doc.getText());
              console.log("[Revert] Retrieved old content for:", filePath);
              // Replace content in editable file
              const document = await vscode.workspace.openTextDocument(
                diffView.editableUri
              );
              const edit = new vscode.WorkspaceEdit();
              edit.replace(
                diffView.editableUri,
                new vscode.Range(0, 0, document.lineCount, 0),
                oldContent
              );
              // Apply and save changes
              await vscode.workspace.applyEdit(edit);
              await document.save();
              // Find only the specific diff editor tab for this file
              const allTabs = vscode.window.tabGroups.all.flatMap(
                (group) => group.tabs
              );
              const tabsToClose = allTabs.filter((tab) => {
                if (!tab.input || typeof tab.input !== "object") {
                  return false;
                }
                // For diff editor tabs
                if ("original" in tab.input && "modified" in tab.input) {
                  const original = tab.input.original.toString();
                  const modified = tab.input.modified.toString();
                  // Strict matching to ensure we only close the exact diff view
                  const isExactMatch =
                    (original.includes(`diff-readonly://left/${diffView.fileName}`) ||
                     original.includes(`diff-readonly://right/${diffView.fileName}`)) &&
                    (modified.includes(fileUriString) ||
                     modified.includes(`diff-readonly://right/${diffView.fileName}`));
                  if (isExactMatch) {
                    console.log("[Revert] Found exact matching diff tab:", {
                      original,
                      modified,
                      fileName: diffView.fileName
                    });
                  }
                  return isExactMatch;
                }
                // For standalone diff-readonly tabs of this specific file
                if ("uri" in tab.input) {
                  const uri = tab.input.uri.toString();
                  const isExactMatch = uri.includes("diff-readonly://") &&
                                      uri.includes(diffView.fileName) &&
                                      (uri.includes(`/left/${diffView.fileName}`) ||
                                       uri.includes(`/right/${diffView.fileName}`));
                  if (isExactMatch) {
                    console.log("[Revert] Found exact matching readonly tab:", uri);
                  }
                  return isExactMatch;
                }
                return false;
              });
              console.log("[Revert] Tabs to close:", tabsToClose.length);
              // Close only the specific tabs for this file
              for (const tab of tabsToClose) {
                try {
                  await vscode.window.tabGroups.close(tab);
                  console.log("[Revert] Closed specific tab:", tab.label);
                } catch (error) {
                  console.error("[Revert] Error closing tab:", {
                    label: tab.label,
                    error,
                  });
                }
              }
              // Remove only this file from active diffs
              activeDiffViews.delete(fileUriString);
              // Open the original file with reverted content
              await vscode.window.showTextDocument(document, {
                viewColumn: vscode.ViewColumn.One,
                preview: false,
              });
              console.log(`[Revert] Successfully reverted changes for: ${filePath}`);
            } catch (error) {
              console.error("[Revert] Error reverting changes:", error);
              throw error;
            }
          } else {
            console.log("[Revert] No active diff view found for:", filePath);
          }
        }
      } catch (error) {
        console.error(`Error processing file ${filePath}:`, error);
        // If we can't open the document, try direct deletion
        try {
          await vscode.workspace.fs.delete(fileUri);
          console.log(`Successfully deleted file: ${filePath}`);
        } catch (deleteError) {
          console.error(
            `Error in direct deletion of ${filePath}:`,
            deleteError
          );
        }
      }
    }
    // After processing all files, handle root directory deletion if needed
    if (rootDir) {
      await deleteFolder(rootDir);
    }
  } catch (error) {
    console.error("Error handling revert for multiple files:", error);
    vscode.window.showErrorMessage(
      "Failed to revert files. Check logs for details."
    );
    throw error;
  }
}

export async function deleteFiles(
  filePaths: string[],
  rootDir: string | null, deleteRoot: boolean = false
) {
  try {
    const workspaceUri = vscode.workspace.workspaceFolders?.[0]?.uri;
    if (!workspaceUri) {
      throw new Error("No workspace is open.");
    }

    for (const filePath of filePaths) {
      const fileUri = vscode.Uri.joinPath(workspaceUri, filePath);

      // Skip non-file URIs (e.g. diff-readonly://)
      if (fileUri.scheme !== "file") {
        console.warn(`Skipping non-file URI: ${fileUri.toString()}`);
        continue;
      }
      // Attempt to close any open tabs for this file
      const tabsToClose: vscode.Tab[] = [];
      vscode.window.tabGroups.all.forEach((group) => {
        group.tabs.forEach((tab) => {
          if (
            tab.input &&
            tab.input.uri &&
            tab.input.uri.fsPath === fileUri.fsPath
          ) {
            tabsToClose.push(tab);
          }
        });
      });

      for (const tab of tabsToClose) {
        try {
          await vscode.window.tabGroups.close(tab);
          console.log(`Closed tab for: ${filePath}`);
        } catch (closeError) {
          console.error(`Error closing tab for ${filePath}:`, closeError);
        }
      }

      // Delete the file
      try {
        await vscode.workspace.fs.delete(fileUri);
        console.log(`Successfully deleted file: ${filePath}`);
      } catch (deleteError) {
        console.error(`Error deleting file ${filePath}:`, deleteError);
      }
    }
 
    // Only delete the root directory if explicitly requested
    if (deleteRoot && rootDir) {
      try {
        await deleteFolder(rootDir);
        console.log(`Successfully deleted root directory: ${rootDir}`);
      } catch (deleteFolderError) {
        console.error(`Error deleting root directory ${rootDir}:`, deleteFolderError);
      }
    }
  } catch (error) {
    console.error("Error deleting multiple files:", error);
    vscode.window.showErrorMessage(
      "Failed to delete files. Check logs for details."
    );
    throw error;
  }
}

// Update saveBuildFile function to use editableUri
export async function saveBuildFile(filePath: string) {
  try {
    console.log(`[Save Build] Saving file: ${filePath}`);

    const workspaceUri = vscode.workspace.workspaceFolders?.[0]?.uri;
    if (!workspaceUri) {
      throw new Error("No workspace is open.");
    }

    const fileUri = vscode.Uri.joinPath(workspaceUri, filePath);
    const fileUriString = fileUri.toString();

    // Extract filename from filePath or URI
    const fileName = filePath.split("/").pop() || path.basename(fileUriString);
    console.log("[Save Build] Looking for file:", fileName);

    // Get all open tabs
    const allTabs = vscode.window.tabGroups.all.flatMap((group) => group.tabs);
    console.log("[Save Build] Total open tabs:", allTabs.length);

    // Find matching tabs
    const matchingTabs = allTabs.filter((tab) => {
      if (!tab.input) return false;

      let tabFileName = "";
      if ("uri" in tab.input) {
        // Regular editor tab
        tabFileName = path.basename(tab.input.uri.fsPath);
      } else if ("original" in tab.input) {
        // Diff editor tab
        const modified = tab.input.modified;
        if ("path" in modified) {
          tabFileName = path.basename(modified.path);
        }
      }

      // console.log("[Save Build] Checking tab:", {
      //   tabName: tab.label,
      //   fileName: tabFileName,
      //   isDirty: tab.isDirty,
      // });

      return tabFileName.toLowerCase() === fileName.toLowerCase();
    });

    console.log("[Save Build] Found matching tabs:", matchingTabs.length);

    // Save matching dirty tabs
    for (const tab of matchingTabs) {
      if (tab.isDirty) {
        if ("uri" in tab.input) {
          const document = await vscode.workspace.openTextDocument(
            tab.input.uri
          );
          await document.save();
          console.log("[Save Build] Saved dirty tab:", fileName);
        }
      }
    }

    // Check if this file has an active diff view
    const diffView = activeDiffViews.get(fileUriString);
    if (diffView) {
      console.log("[Save Build] Found active diff view for:", filePath);
      console.log("[Save Build] DiffView details:", {
        originalUri: diffView.originalUri.toString(),
        editableUri: diffView.editableUri.toString(),
        diffUri: diffView.diffUri.toString(),
        fileName: diffView.fileName,
      });

      // Find all diff editor tabs related to this file
      const allTabs = vscode.window.tabGroups.all.flatMap(
        (group) => group.tabs
      );
      console.log("[Save Build] Total tabs found:", allTabs.length);

      const diffTabs = allTabs.filter((tab) => {
        if (!tab.input || !("original" in tab.input)) {
          return false;
        }

        // Log tab details for debugging
        const original = tab.input.original.toString();
        const modified = tab.input.modified.toString();
        // console.log("[Save Build] Checking tab:", {
        //   original,
        //   modified,
        //   isDiffEditor: "original" in tab.input,
        // });

        // Check if this tab is comparing our target file
        const isMatch =
          original.includes(`diff-readonly://left/${diffView.fileName}`) ||
          original.includes(`diff-readonly://right/${diffView.fileName}`) ||
          modified.includes(`diff-readonly://left/${diffView.fileName}`) ||
          modified.includes(`diff-readonly://right/${diffView.fileName}`);

        if (isMatch) {
          console.log("[Save Build] Found matching diff tab:", {
            original,
            modified,
            fileName: diffView.fileName,
          });
        }

        return isMatch;
      });

      console.log("[Save Build] Matching diff tabs found:", diffTabs.length);

      // Close all related diff tabs
      for (const tab of diffTabs) {
        await vscode.window.tabGroups.close(tab);
        console.log("[Save Build] Closed diff tab for:", filePath);
      }

      // Reopen the editable file
      const document = await vscode.workspace.openTextDocument(
        diffView.editableUri
      );
      await vscode.window.showTextDocument(document, {
        viewColumn: vscode.ViewColumn.One,
        preview: false,
      });

      // Remove from active diffs
      activeDiffViews.delete(fileUriString);
      console.log("[Save Build] Reopened original file:", filePath);
    }
  } catch (error) {
    console.error("[Save Build] Error saving file:", error);
    vscode.window.showErrorMessage(`Failed to save file: ${filePath}`);
    throw error;
  }
}
export async function retrieveCode(
  filePaths: string[],
  rootDir: string | null
) {
  try {
    const workspaceUri = vscode.workspace.workspaceFolders?.[0]?.uri;
    if (!workspaceUri) {
      throw new Error("No workspace is open.");
    }

    // Object to store the retrieved code for each file
    const retrievedCodes: { [filePath: string]: string } = {};

    for (const filePath of filePaths) {
      const fileUri = vscode.Uri.joinPath(workspaceUri, filePath);
      try {
        const document = await vscode.workspace.openTextDocument(fileUri);
        let codeContent = "";

        if (document.isDirty) {
          // For dirty documents, retrieve the unsaved content.
          codeContent = document.getText();
          console.log(`Retrieved unsaved code for dirty file: ${filePath}`);
        } else {
          // For non-dirty documents, check if there's an active diff view.
          const fileUriString = fileUri.toString();
          const diffView = activeDiffViews.get(fileUriString);
          if (diffView) {
            console.log(`[Retrieve] Found active diff view for: ${filePath}`);
            // Retrieve the old content from the diff view.
            codeContent = await vscode.workspace
              .openTextDocument(diffView.diffUri)
              .then((doc) => doc.getText());
            console.log(`[Retrieve] Retrieved code from diff view for: ${filePath}`);
          } else {
            // Simply retrieve the saved content.
            codeContent = document.getText();
            console.log(`[Retrieve] Retrieved saved code for: ${filePath}`);
          }
        }

        // Store the retrieved code.
        retrievedCodes[filePath] = codeContent;

        // Optionally, open the document in the editor.
        await vscode.window.showTextDocument(document, {
          viewColumn: vscode.ViewColumn.One,
          preview: false,
        });
      } catch (error) {
        console.error(`Error retrieving code for file ${filePath}:`, error);
      }
    }

    // Note: We do not delete the root directory in this function.
    return retrievedCodes;
  } catch (error) {
    console.error("Error retrieving code from multiple files:", error);
    vscode.window.showErrorMessage("Failed to retrieve code. Check logs for details.");
    throw error;
  }
}


/**
 * Reads the content of files specified in the semanticResults object.
 *
 * @param {Object} semanticResults - The object containing relevant_files array.
 * @returns {Promise<string[]>} - A promise that resolves to an array of file contents.
 * @throws {Error} - If semanticResults is invalid.
 */
interface SemanticResult {
  relevant_history: Array<{
    interaction_number: number;
    relevant_content: string;
    relevance_reason: string;
  }>;
  relevant_files: Array<{
    file_path: string;
    relevance_reason: string;
  }>;
}
export async function getRelevantFilesContent(semanticResults: SemanticResult, rootDirectory: string) {
  console.log(semanticResults, rootDirectory,"getRelevantFilesContentCv")
  if (!semanticResults || !Array.isArray(semanticResults.relevant_files)) {
    throw new Error('Invalid semanticResults object');
  }

  // Map each file object to a promise that resolves with its content.
  const fileContentPromises = semanticResults.relevant_files.map(async (fileObj) => {
    let filePath = path.join(rootDirectory, fileObj.file_path); 
 
    try {
      const content = await fs.readFileSync(filePath, 'utf8');
      console.log(content, "reading_content_filepath");
      
      return content;
    } catch (error) {
      console.error(`Error reading file at ${filePath}:`, error);
      // Optionally, you could rethrow or simply return null.
      return null;
    }
  });

  // Wait for all file-read operations to complete.
  const fileContents = await Promise.all(fileContentPromises);
  // Filter out any null results from failed reads.
  return fileContents.filter(content => content !== null);
}