//@ts-ignore
/* eslint-disable */
// @ts-nocheck
import * as vscode from 'vscode';
import { getTestableCodes1, getLanguageType } from './CodeParser';

export async function processCodeBlocks(editor: vscode.TextEditor) {
    try {
        if (!editor) {
            console.log('No active editor found');
            return { detectedCode: [] };
        }

        const language = getLanguageType(editor.document.languageId);
        const codeBlocks = await getTestableCodes1(editor.document, editor);
        const filename = editor.document.fileName;
        console.log(filename, "filename got");

        // Debug log to see exact structure
        console.log("Raw code blocks structure:", JSON.stringify(codeBlocks, null, 2));

        if (codeBlocks.detectedCode && Array.isArray(codeBlocks.detectedCode)) {
            const processedBlocks = codeBlocks.detectedCode.map(block => {
                try {
                    if (!block || !block.range) {
                        console.warn("Block missing range:", block);
                        return null;
                    }

                    // Debug log for each block's range
                    console.log("Processing block:", block.name);
                    console.log("Range data:", JSON.stringify(block.range, null, 2));

                    // Extract range information, handling both array and object formats
                    let startLine, endLine, startChar, endChar;

                    if (block.range[0] && block.range[1]) {
                        startLine = Number(block.range[0].line);
                        endLine = Number(block.range[1].line);
                        startChar = Number(block.range[0].character);
                        endChar = Number(block.range[1].character);
                    } else if (block.range.start && block.range.end) {
                        startLine = Number(block.range.start.line);
                        endLine = Number(block.range.end.line);
                        startChar = Number(block.range.start.character);
                        endChar = Number(block.range.end.character);
                    } else {
                        console.warn("Unexpected range format:", block.range);
                        return null;
                    }

                    // Validate the extracted numbers
                    if (isNaN(startLine) || isNaN(endLine) || isNaN(startChar) || isNaN(endChar)) {
                        console.warn("Invalid line numbers detected:", { startLine, endLine, startChar, endChar });
                        return null;
                    }

                    // Ensure valid line numbers
                    startLine = Math.max(0, startLine);
                    endLine = Math.min(editor.document.lineCount - 1, endLine);

                    const startPos = new vscode.Position(startLine, startChar);
                    const endPos = new vscode.Position(endLine, endChar);
                    const range = new vscode.Range(startPos, endPos);
                    
                    // Get the actual code content for this block
                    const codeContent = editor.document.getText(range);

                    const processedBlock = {
                        name: block.name,
                        range: {
                            start: { line: startLine, character: startChar },
                            end: { line: endLine, character: endChar }
                        },
                        content: codeContent,
                        kind: block.kind,
                        language
                    };

                    // Log detailed information for each block
                    // console.log(`\nBlock: ${processedBlock.name}`);
                    // console.log(`Range: Lines ${startLine + 1}-${endLine + 1}`);
                    // console.log('Code content:');
                    // console.log('------------------------');
                    // console.log(codeContent);
                    // console.log('------------------------');

                    return processedBlock;
                } catch (err) {
                    console.error("Error processing block:", err, "Block:", JSON.stringify(block, null, 2));
                    return null;
                }
            }).filter(block => block !== null);

            // Log summary of all blocks
            console.log('\nSummary of all code blocks:');
            processedBlocks.forEach((block, index) => {
                console.log(`\n${index + 1}. ${block.name}`);
                console.log(`   Lines: ${block.range.start.line + 1}-${block.range.end.line + 1}`);
                console.log(`   Kind: ${block.kind}`);
                console.log(`   Length: ${block.content.length} characters`);
            });

            return { 
                detectedCode: processedBlocks,
                summary: processedBlocks.map(block => ({
                    name: block.name,
                    lineRange: `${block.range.start.line + 1}-${block.range.end.line + 1}`,
                    codeLength: block.content.length
                })),
                filename: filename
            };
        }

        return { detectedCode: [], summary: [], filename: filename };
    } catch (error) {
        console.error("Error processing code blocks:", error);
        return { detectedCode: [], summary: [], filename: '' };
    }
} 