//@ts-ignore
import * as vscode from "vscode"; 
import { runCommandSync } from "./util";  
//@ts-ignore
const fs = require("fs");
const cp = require('child_process');
const { exec } = require('child_process');
const { spawnSync } = require('child_process');
const { PythonShell } = require('python-shell');
const { execSync } = require('child_process');

export function getTestCommand(
    language: string,
    framework: string,
    testFilePath: string,
    testFileName: string,
    outputFilePath: string
): string {
    if (!language || !framework || !testFilePath || !testFileName || !outputFilePath) {
        throw new Error("Missing required parameters.");
    }
    const os = require("os");
    const platform = os.platform();
    const testDirectory = path.dirname(testFilePath);

    let command = "";

    switch (language.toLowerCase()) {
        case "javascript":
        case "typescript": {
            const jsFramework = framework.toLowerCase();
            const installFramework =
                jsFramework === "react testing library"
                    ? platform === "win32"
                        ? `npm install --save-dev @testing-library/react @testing-library/jest-dom @testing-library/user-event && `
                        : `npm install --save-dev @testing-library/react @testing-library/jest-dom @testing-library/user-event; `
                    : platform === "win32"
                        ? `npm install --save-dev ${jsFramework} && `
                        : `npm install --save-dev ${jsFramework}; `;

            if (jsFramework === "mocha") {
                command = `${installFramework}cd ${testDirectory} && npx mocha "${testFileName}" --reporter spec > "${outputFilePath}" 2>&1`;
            } else if (jsFramework === "jest" || jsFramework === "react testing library") {
                command = `${installFramework}cd ${testDirectory} && npx jest --testPathPattern="${testFileName}" --verbose --passWithNoTests > "${outputFilePath}" 2>&1`;
            }
            break;
        }
        case "python": {
            const installFramework =
                platform === "win32"
                    ? `pip install coverage && pip install ${framework}`
                    : `pip3 install coverage; pip3 install ${framework}`;

            if (framework === "pytest") {
                command =
                    platform === "win32"
                        ? `${installFramework} && cd ${testDirectory} && python -m pytest ${testFileName} -v > "${outputFilePath}" 2>&1`
                        : `${installFramework}; cd ${testDirectory}; python3 -m pytest ${testFileName} -v > "${outputFilePath}" 2>&1`;
            } else if (framework === "unittest") {
                command =
                    platform === "win32"
                        ? `cd ${testDirectory} && python -m unittest ${testFileName} -v > "${outputFilePath}" 2>&1`
                        : `cd ${testDirectory}; python3 -m unittest ${testFileName} > "${outputFilePath}" 2>&1`;
            }
            break;
        }
        // case "go": {
        //   const installFramework = `go get github.com/stretchr/testify/assert@latest`;
        //   const goModFilePath = path.join(testDirectory, "go.mod");

        //   if (!fs.existsSync(goModFilePath)) {
        //     const goModInitCommand = `cd ${testDirectory} && go mod init gocodeo_tests`;
        //     runCommandSync(goModInitCommand);
        //   }

        //   const installTestifyCommand = `cd ${testDirectory} && ${installFramework} && go mod tidy`;
        //   runCommandSync(installTestifyCommand);

        //   command = `cd ${testDirectory} && go test -v ${testFileName} > "${outputFilePath}" 2>&1`;
        //   break;
        // }
        case "go": {
            if (framework.toLowerCase() === "testify") {
                const installFramework = `go get github.com/stretchr/testify/assert@latest`;
                const goModFilePath = path.join(testDirectory, "go.mod");

                if (!fs.existsSync(goModFilePath)) {
                    const goModInitCommand = `cd ${testDirectory} && go mod init gocodeo_tests`;
                    runCommandSync(goModInitCommand);
                }

                const installTestifyCommand = `cd ${testDirectory} && ${installFramework} && go mod tidy`;
                runCommandSync(installTestifyCommand);

                command = `cd ${testDirectory} && go test -v ${testFileName} > "${outputFilePath}" 2>&1`;
            } else if (framework.toLowerCase() === "ginkgo") {
                const installGinkgo = `go install github.com/onsi/ginkgo/v2/ginkgo@latest`;
                const goModFilePath = path.join(testDirectory, "go.mod");

                if (!fs.existsSync(goModFilePath)) {
                    const goModInitCommand = `cd ${testDirectory} && go mod init gocodeo_tests`;
                    runCommandSync(goModInitCommand);
                }

                const installGinkgoCommand = `cd ${testDirectory} && ${installGinkgo} && go mod tidy`;
                runCommandSync(installGinkgoCommand);

                command = `cd ${testDirectory} && ginkgo -v > "${outputFilePath}" 2>&1`;
            }
            break;
        }

        case "java": {
            command = `cd ${testDirectory} && mvn test -Dtest=${testFileName} > "${outputFilePath}" 2>&1`;
            break;
        }
        default:
            throw new Error(`Unsupported language: ${language}`);
    }

    return command;
}