export const fileContentsForFastifyProject = [
    {
        filename: ".gitignore",
        content: `node_modules
.env
package-lock.json
`,
    },
    {
        filename: "index.js",
        content: `
const fastify = require("fastify")({ logger: true });

// Define the "/" route
fastify.get("/", async (request, reply) => {
  return { message: "Welcome to Fastify!" };
});

// Start the server
const start = async () => {
  try {
    await fastify.listen({ port: 3000 });
    console.log("Server is running on http://localhost:3000");
  } catch (err) {
    fastify.log.error(err);
    process.exit(1);
  }
};

start();
`
    },

    {
        filename: "package.json",
        content: `{
  "name": "fastify_project",
  "version": "1.0.0",
  "main": "index.js",
  "scripts": {
    "test": "echo \\\"Error: no test specified\\\" && exit 1"
  },
  "keywords": [],
  "author": "",
  "license": "ISC",
  "description": "",
  "dependencies": {
    "fastify": "^5.2.1"
  }
}

`,
    },

];

export default fileContentsForFastifyProject;
