export function extractAllGoImports(sourceCode: any) {
    // Regular expression to match all import blocks
    const importRegex = /import\s+\(([\s\S]*?)\)/g;

    // Use matchAll to find all matches
    const matches = [...sourceCode.matchAll(importRegex)];

    // Extract and format each match
    const importBlocks = matches.map(match => match[0]); // Return the full matched block for each import

    return importBlocks;
}
export  function extractGoPackage(code) {
    // Match the `package` declaration at the start of the code
    const packageMatch = code.match(/^\s*package\s+(\w+)/m);
    // If found, return the package name, otherwise return null
    return packageMatch ? packageMatch[1] : null;
}
