export function modifyJSCode(sourceCode: any) {
    // Check if the code is JavaScript
    if (sourceCode.main.file.endsWith('.js' || '.ts')) {
        let code = sourceCode.main.code;

        // Remove all 'export' keywords from named exports
        code = code.replace(/export\s+/g, '');

        // Handle 'export default'
        let defaultExport = null;
        const defaultExportRegex = /export\s+default\s+([a-zA-Z0-9_]+);?/;
        const defaultMatch = code.match(defaultExportRegex);
        if (defaultMatch) {
            defaultExport = defaultMatch[1]; // Capture the default export name
            code = code.replace(defaultExportRegex, ''); // Remove 'export default' line
        }

        // Remove standalone 'default X;' (e.g., 'default Home;')
        const standaloneDefaultRegex = /default\s+([a-zA-Z0-9_]+);?/;
        const standaloneDefaultMatch = code.match(standaloneDefaultRegex);
        if (standaloneDefaultMatch) {
            defaultExport = standaloneDefaultMatch[1]; // Update default export
            code = code.replace(standaloneDefaultRegex, ''); // Remove the line
        }

        // The rest of the logic for handling functions is unnecessary if `module.exports` is not being added
        // Simply return the modified code
        return code;
    }

    // Return the code as is if it's not JavaScript
    return sourceCode.main.code;
}


