export const waitForFile = async (filePath: string, timeout: number = 5000) => {
  const startTime = Date.now();

  while (!fs.existsSync(filePath)) {
    if (Date.now() - startTime > timeout) {
      throw new Error(`File not found: ${filePath} after ${timeout} ms`);
    }
    await new Promise(resolve => setTimeout(resolve, 100));
  }
};

export function runCommandSync(command: string) {
  console.log("Running command:", command);
  try {
    const result = spawnSync(command, {
      shell: true,
      stdio: 'pipe',  // Capture output for custom handling
    });

    // Write stdout and stderr to gocodeo_out.txt
    const outputFilePath = path.join(process.cwd(), 'gocodeo_out.txt');
    fs.writeFileSync(outputFilePath, result.stdout + result.stderr);
    console.log(result.stdout + result.stderr, "outputFilePath")
    if (result.error) {
      console.error(`Command Error: ${result.error.message}`);
    }
    if (result.stderr.toString().trim()) {
      console.error(`Command Stderr: ${result.stderr.toString()}`);
    }
    if (result.stdout.toString().trim()) {
      console.log(`Command Stdout: ${result.stdout.toString()}`);
    }
    console.log(outputFilePath, "outputFilePath")
    if (result.error) {
      console.log(`Error executing command ${result.error.message}`);
      // vscode.window.showErrorMessage(`Error executing command: ${result.error.message}`);
    } else if (result.status !== 0) {
      console.log(`Command exited with status code: ${result.status}`);
      // vscode.window.showErrorMessage(`Command exited with status code ${result.status}`);
    } else {
      console.log(`Command executed successfully`);
      // vscode.window.showInformationMessage(`Command executed successfully`);
    }
  } catch (error) {
    console.error(`Error executing command: ${error.message}`);
    // vscode.window.showErrorMessage(`Error executing command: ${error.message}`);
  }
}

//@ts-ignore
export async function saveFile(filePath, fileName, fileText) {
  fileName = fileName.replace(/GoCodeo-/, "");
  let options = {
    defaultUri: vscode.Uri.file(path.join(filePath, "..", fileName)),
    saveLabel: "Save As",
    filters: {
      "All files": ["*"],
    },
  };

  let uri = await vscode.window.showSaveDialog(options);
  if (uri) {
    // vscode.window.showInformationMessage("test file uri", uri, uri.fsPath);
    await fs.writeFileSync(uri.fsPath, fileText);
    // Update the title of the editor to the new file name
    //@ts-ignore
    vscode.window.showInformationMessage(uri.file);
    const curDoc = await vscode.workspace.openTextDocument(uri);
    await vscode.window.showTextDocument(curDoc);

  }
}



// Helper function: Recursively build a file structure tree
export function getFileStructure(dirPath: string): any[] {
  let result: any[] = [];
  try {
    const items = fs.readdirSync(dirPath);
    items.forEach(item => {
      const fullPath = path.join(dirPath, item);
      const stats = fs.statSync(fullPath);
      if (stats.isDirectory()) {
        result.push({
          name: item,
          type: 'directory',
          children: getFileStructure(fullPath)
        });
      } else {
        result.push({
          name: item,
          type: 'file'
        });
      }
    });
  } catch (error) {
    console.error("Error reading directory:", error);
  }
  return result;
}

export function getAllFilesRecursively(dir: string, baseDir: string = dir): string[] {
  let results: string[] = [];
  const list = fs.readdirSync(dir);
  list.forEach((file: string) => {
    const fullPath = path.join(dir, file);
    const stat = fs.statSync(fullPath);
    if (stat && stat.isDirectory()) {
      results = results.concat(getAllFilesRecursively(fullPath, baseDir));
    } else {
      // Compute the relative file path from baseDir.
      const relativePath = path.relative(baseDir, fullPath).replace(/\\/g, '/');
      results.push(relativePath);
    }
  });
  return results;
}

 
