/**
 * Determines the path to the state.vscdb file based on the operating system.
 * @returns {string} The file path to state.vscdb
 */
export function getStateDbPath() {
  let appData;
  if (process.platform === 'win32') {
    appData = process.env.APPDATA || path.join(os.homedir(), 'AppData', 'Roaming');
  } else if (process.platform === 'darwin') {
    appData = path.join(os.homedir(), 'Library', 'Application Support');
  } else {
    appData = path.join(os.homedir(), '.config');
  }
  return path.join(appData, 'Code', 'User', 'globalStorage', 'state.vscdb');
}

 
export async function getRecentPaths(db) {
  return new Promise((resolve, reject) => {
    db.get(
      "SELECT value FROM ItemTable WHERE key = 'history.recentlyOpenedPathsList'",
      (err, row) => {
        if (err) {
          reject(err);
        } else if (row) {
          try {
            const recentPaths = JSON.parse(row.value);
            resolve(recentPaths);
          } catch (parseErr) {
            reject(parseErr);
          }
        } else {
          resolve(null);
        }
      }
    );
  });
}
 
export async function folderExists(uri) {
  try {
    await vscode.workspace.fs.stat(uri);
    return true;
  } catch (error) {
    return false;
  }
}
 

 // Function to find the root directory (assumes it contains package.json or similar)
export  async function findRootDir(startPath: string): Promise<string | null> {
    const fs = require('fs').promises;
    const path = require('path');

    let currentPath = startPath;

    // Keep going up until we can't anymore
    while (currentPath && currentPath !== path.dirname(currentPath)) {
        try {
            // Check for common root directory indicators
            const files = await fs.readdir(currentPath);
            if (
                files.includes('package.json') ||
                files.includes('.git') ||
                files.includes('node_modules')
            ) {
                // Found the root directory
                return path.basename(currentPath);
            }

            // Move up one directory
            currentPath = path.dirname(currentPath);
        } catch (error) {
            console.error('Error reading directory:', error);
            return null;
        }
    }

    return null;
}

 
