//@ts-nocheck
import { log } from "console";
import * as path from "path";
import * as vscode from "vscode";
const fs = require("fs");
const util = require('util');
 
 
class PyParser {
    //@ts-ignore
    isValid(line, document) {
        return [vscode.SymbolKind.Function, vscode.SymbolKind.Class].includes(line.kind);
    }
}
class JSParser {
    //@ts-ignore
    isValid(line, document) {
        if (line.name.endsWith(" callback") || "<function>" === line.name || "<unknown>" === line.name) return false;
        if (line.kind === vscode.SymbolKind.Variable) {
            const regExp =
                /(?:async\s*)?(?:(?:function\s*\([\s\S]*?\)\s*(?::\s*[A-Za-z_$][A-Za-z0-9_$]*\s*)?{[\s\S]*?})|(?:\([\s\S]*?\)\s*(?::\s*[<?A-Za-z_>?$][<?A-Za-z0-9_>?$]*\s*)?=>\s*{[\s\S]*?}))/;
            const regex = new RegExp(line.name + "\\s*=\\s*" + regExp.source);
            const code = document.getText(line.range);
            if (regex.test(code)) return true;
        }
        return [vscode.SymbolKind.Class, vscode.SymbolKind.Function].includes(line.kind);
    }
}
// ! JavaParser Class 
class JavaParser {
    //@ts-ignore
    isValid(line, document) {
        return [vscode.SymbolKind.Method, vscode.SymbolKind.Class].includes(line.kind);
    }
}
// Define the GoParser class
class GoParser {
    //@ts-ignore
    isValid(line, document) {
        return [vscode.SymbolKind.Function, vscode.SymbolKind.Struct].includes(line.kind);
    }
}

class CppParser {
    //@ts-ignore
    isValid(line, document) {
        return [vscode.SymbolKind.Function, vscode.SymbolKind.Class].includes(line.kind);
    }
}
class CsharpParser {
    //@ts-ignore
    isValid(line, document) {
        return [vscode.SymbolKind.Method, vscode.SymbolKind.Class].includes(line.kind);
    }
}
class PhpParser {
    isValid(line: any) {
        // For PHP, we check if the line represents a function or a class
        return line.kind === vscode.SymbolKind.Function || line.kind === vscode.SymbolKind.Class;
    }
}

class RubyParser {
    //@ts-ignore
    isValid(line, document) {
        return [vscode.SymbolKind.Function, vscode.SymbolKind.Class].includes(line.kind);
    }
}
class RustParser {
    //@ts-ignore
    isValid(line, document) {
        return [vscode.SymbolKind.Function, vscode.SymbolKind.Struct, vscode.SymbolKind.Interface].includes(line.kind);
    }
}


