const checkFile = async (file: string) => {
    return new Promise((resolve, reject) => {
        //@ts-ignore
        fs.lstat(file, (err, stats) => {
            if (err)
                //@ts-ignore
                return resolve();
            return resolve(stats);
        });
    })
};

