export async function getPythonImportedCodeFiles(codePath, document) {
    console.log("getPythonImportedCodeFiles called with codePath:", codePath);
    console.log("getPythonImportedCodeFiles called with document:", document);

    const payload = {};
    const imports = [];
    const importRegex1 = /\s*from\s*((\.{0,2}\w+\.?)+)\s+import\s*\(?((\s*\w+\s*,?)+)\s*\)?/gm;
    let matches;
    let importLine = "";

    codePath = codePath.split('/');
    let codeFile = codePath.pop();
    let rootPath = [];

    console.log("Parsing codePath:", codePath);
    for (const dir of codePath) {
        rootPath.push(dir);
        if (dir.includes('frontend-service'))
            break;
    }
    rootPath = rootPath.join('/');
    codePath = codePath.join('/');
    console.log("Root path identified as:", rootPath);

    const LocNo = document.lineCount > 50 ? 50 : document.lineCount;
    for (let i = 0; i < LocNo; i++) {
        const line = document.lineAt(i).text.replace(/\n/, "");
        if (line.includes("#")) continue;
        importLine += line;

        while ((matches = importRegex1.exec(importLine)) !== null) {
            if (matches.length) {
                console.log("Import match found:", matches);
                importLine = "";
                let funcs = matches[3] || "";
                if (funcs) funcs = funcs.split(",");

                const importedFuncs = [];
                for (let func of funcs) {
                    func = func.trim();
                    if (func) importedFuncs.push(func);
                }
                imports.push({ imports: importedFuncs, file: matches[1] });
            }
        }
    }

    console.log("Processed imports:", imports);

    const priortyImports1 = [];
    const priortyImports2 = [];
    let finalImports = [];
    for (let row of imports) {
        let file = row.file;
        if (file.startsWith(".")) {
            console.log("Processing relative import:", file);
            row.file = await getPyFile(codePath, file);
            priortyImports1.push(row);
        }
        else if (file.startsWith("..")) {
            console.log("Processing parent import:", file);
            row.file = await getPyFile(codePath, file);
            priortyImports2.push(row);
        }
    }

    finalImports = [...priortyImports1, ...priortyImports2].slice(0, 4);
    console.log("Final prioritized imports:", finalImports);
    //@ts-ignore
    codePath = path.join(rootPath, codeFile);
    console.log("Resolved codePath for main file:", codePath);

    for (const row of finalImports) {
        try {
            const fileData = await readFile(row.file);
            console.log("Read code for import:", row.file);//@ts-ignore
            row.code = fileData;
        } catch (error) {
            console.warn(`Could not read file ${row.file}:`, error);
        }
    }
    //@ts-ignore
    payload.imports = finalImports;//@ts-ignore
    payload.main = { file: codePath, code: document.getText() };

    console.log("Payload for Python imports and main file:", payload);
    return payload;
}

//@ts-ignore
export async function getJSImportedCodeFiles(codePath, document) {
    // console.log("here");
    const payload = {};
    //@ts-ignore
    const imports = [];
    const importRegex1 = /^import\s(.*)\s+from\s+["'](.*)["'];?$/gm;
    const importRegex2 = /\w+\s(.*)\s+=\s+require\(["'](.*)["']\);?$/gm;
    const funcsRegex = /(\*\s+as\s+|{|})+/gm;
    let matches1;
    let matches2;
    let matches;
    let importLine = "";
    //@ts-ignore
    codePath = codePath.split('/');
    //@ts-ignore
    let codeFile = codePath.pop();
    let rootPath = [];
    for (const dir of codePath) {
        rootPath.push(dir);
        if (dir.includes('frontend-service'))
            break;
    }
    //@ts-ignore
    rootPath = rootPath.join('/');
    // console.log (document.lineCount);
    //@ts-ignore
    codePath = codePath.join('/');
    const LocNo = document.lineCount > 50 ? 50 : document.lineCount;
    for (let i = 0; i < LocNo; i++) {
        const line = document.lineAt(i).text.replace(/\n/, "");
        if (line.includes("//") || line.includes('/*')) continue;
        importLine += line;
        // console.log(importLine);
        // console.log('loc', importLine);
        while ((matches1 = importRegex1.exec(importLine)) !== null || (matches1 = importRegex2.exec(importLine)) !== null) {
            // console.log('matches', matches);
            //@ts-ignore
            matches = matches1 && matches1.length ? matches1 : matches2;
            //@ts-ignore
            if (matches1 && matches1.length && matches1.index === importRegex1.lastIndex) {
                importRegex1.lastIndex++;
            }
            //@ts-ignore
            if (matches2 && matches2.length && matches2.index === importRegex2.lastIndex) {
                importRegex2.lastIndex++;
            }
            if (matches && matches.length) {
                importLine = "";
                let funcs = matches[1];
                //@ts-ignore
                funcs = funcs.replaceAll(funcsRegex, "");
                //@ts-ignore
                funcs = funcs.split(",");
                const importedFuncs = [];
                for (let func of funcs) {
                    func = func.trim();
                    if (func) importedFuncs.push(func);
                }
                imports.push({ imports: importedFuncs, file: matches[2] });
            }
        }
    }
    const priortyImports1 = [];
    const priortyImports2 = [];
    let finalImports = [];
    for (let row of imports) {
        let file = row.file;
        if (file.toLocaleLowerCase().includes("/style")) continue;
        else if (file.startsWith("./")) {
            row.file = await getJSFile(codePath, file);
            priortyImports1.push(row);

        }
        else if (file.startsWith("../")) {
            row.file = await getJSFile(codePath, file);
            priortyImports2.push(row);

        }
        else if (file.includes("/")) {
            //@ts-ignore
            row.file = await getJSFile(rootPath, file);
            finalImports.push(row);
        }
    }
    //@ts-ignore
    finalImports = [...priortyImports1, ...priortyImports2, ...finalImports].slice(0, 4);
    for (let row of finalImports) {
        //@ts-ignore
        // row.file = row.file.replace(rootPath, serverPath);
        row.file = row.file;
    }

    // codePath = codePath.replace(rootPath, '');
    let testFiles = codeFile.split('.');
    let testExt = testFiles.pop();
    //@ts-ignore
    const testFile = path.join(rootPath, 'tests', codePath, `${testFiles.join('.')}.test.${testExt}`);
    //@ts-ignore
    codePath = path.join(rootPath, codePath, codeFile);
    const testData = await readFile(testFile);
    if (testData) {
        //@ts-ignore
        payload.tests = { file: testFile, imports: ['tests'], code: testData };
    }
    // console.log(finalImports);
    for (const row of finalImports) {
        const fileData = await readFile(row.file);
        //@ts-ignore
        row.code = fileData;
    }
    //@ts-ignore
    payload.imports = finalImports;
    //@ts-ignore
    payload.main = { file: codePath, code: document.getText() };
    // payload.main = {file: codePath, code: await readFile(codePath)};
    return payload
};
 

export async function getJavaImportedCodeFiles(codePath: string, document: any) {
    const payload: { imports?: any[]; main?: { file: string; code: string } } = {};
    const imports: { file: string }[] = [];
    const importRegex = /^\s*import\s+([\w.]+);$/gm;
    let matches;
    let importLine = "";

    // Split and parse the code path
    const codePathArray = codePath.split('/');
    const codeFile = codePathArray.pop() ?? ''; // Ensure `codeFile` is a string

    // Define `rootPath` as a string
    let rootPath = '';

    for (const dir of codePathArray) {
        rootPath += `${dir}/`; // Accumulate path as a string
        if (dir.includes('src')) break; // Stop at the main src directory if applicable
    }
    // Clean up any trailing slashes in `rootPath`
    rootPath = rootPath.replace(/\/$/, '');

    // Define `codePath` as a joined string of the remaining path
    const codePathJoined = codePathArray.join('/');

    // Limit lines for scanning imports
    const LocNo = document.lineCount > 50 ? 50 : document.lineCount;
    for (let i = 0; i < LocNo; i++) {
        const line = document.lineAt(i).text.replace(/\n/, "");
        if (line.includes("//")) continue;
        importLine += line;

        // Match import statements
        while ((matches = importRegex.exec(importLine)) !== null) {
            importLine = "";
            imports.push({ file: matches[1] });
        }
    }

    const finalImports: { file: string; code?: string }[] = [];
    for (const row of imports) {
        row.file = await getJavaFile(rootPath, row.file);
        finalImports.push(row);
    }

    // Read and add code for each import file
    for (const row of finalImports) {
        const fileData = await readFile(row.file);
        row.code = fileData;
    }

    payload.imports = finalImports;
    payload.main = { file: path.join(rootPath, codePathJoined, codeFile), code: document.getText() };

    return payload;
}

const importRegex = /^\s*import\s+"([^"]+)";?$/gm; // Adjusted for Go single-line imports
const importBlockRegex = /^\s*import\s+\(\s*([\s\S]*?)\s*\)/gm; // For Go multiline import blocks

export async function getGoImportedCodeFiles(codePath: string, document: any) {
    const payload: { imports?: any[]; main?: { file: string; code: string } } = {};
    const imports: { file: string }[] = [];
    let matches;
    let importLine = "";

    // Parse the code path to get root directory and file name
    const codePathArray = codePath.split('/');
    const codeFile = codePathArray.pop() ?? '';
    let rootPath = '';

    for (const dir of codePathArray) {
        rootPath += `${dir}/`;
        if (dir.includes('src')) break;
    }
    rootPath = rootPath.replace(/\/$/, '');

    const codePathJoined = codePathArray.join('/');

    // Limit the number of lines to scan for imports
    const LocNo = document.lineCount > 50 ? 50 : document.lineCount;
    for (let i = 0; i < LocNo; i++) {
        const line = document.lineAt(i).text.replace(/\n/, "");
        if (line.includes("//")) continue;
        importLine += line;

        // Match single-line import statements
        while ((matches = importRegex.exec(importLine)) !== null) {
            importLine = "";
            imports.push({ file: matches[1] });
        }

        // Match import block (multi-line) statements
        if ((matches = importBlockRegex.exec(document.getText())) !== null) {
            const blockImports = matches[1].match(/"([^"]+)"/g);
            if (blockImports) {
                blockImports.forEach(imp => {
                    imports.push({ file: imp.replace(/"/g, '') });
                });
            }
        }
    }

    const finalImports: { file: string; code?: string }[] = [];
    for (const row of imports) {
        row.file = await getGoFile(rootPath, row.file);
        finalImports.push(row);
    }

    // Read and add code for each import file
    for (const row of finalImports) {
        const fileData = await readFile(row.file);
        row.code = fileData;
    }

    payload.imports = finalImports;
    payload.main = { file: path.join(rootPath, codePathJoined, codeFile), code: document.getText() };

    return payload;
}

export async function getCppImportedCodeFiles(codePath:any, document:any) {
    console.log("getCppImportedCodeFiles called with codePath:", codePath);
    console.log("getCppImportedCodeFiles called with document:", document);

    const imports = [];
    const importRegex = /^\s*#include\s+"([^"]+)"/gm;
    let matches;

    // Find and store imports
    console.log("Starting to find imports...");
    while ((matches = importRegex.exec(document.getText())) !== null) {
        console.log("Found import:", matches[1]);
        imports.push({ file: matches[1] });
    }

    const finalImports = [];
    for (const row of imports) {
        const filePath = path.join(codePath, `${row.file}.cpp`);
        console.log("Checking filePath for import:", filePath);
        try {
            //@ts-ignore
            const fileData = await readFile(filePath, 'utf8');
            console.log("Read file data for:", filePath);
            finalImports.push({ file: filePath, code: fileData });
        } catch (error) {
            console.warn(`Could not read file ${filePath}:`, error);
        }
    }

    // Main file content
    const mainFileCode = document.getText();
    console.log("Main file content loaded.");

    return {
        imports: finalImports,
        main: { file: path.join(codePath, document.fileName), code: mainFileCode },
    };
}



