// Add PHP-specific function for fetching imported files
export async function getPhpImportedCodeFiles(codePath: string, document: any) {
    const imports = [];
    const importRegex = /^\s*require|include(_once)?\s*\(?\s*['"]([^'"]+)['"]\s*\)?;/gm;
    let matches;

    // Extract imports from PHP file
    while ((matches = importRegex.exec(document.getText())) !== null) {
        imports.push({ file: matches[2] });
    }

    const finalImports: { file: string; code?: string }[] = [];
    for (const row of imports) {
        const filePath = path.join(codePath, row.file);
        try {
            const fileData = await readFile(filePath, 'utf8');
            finalImports.push({ file: filePath, code: fileData });
        } catch (error) {
            console.warn(`Could not read file ${filePath}:`, error);
        }
    }

    return {
        imports: finalImports,
        main: { file: path.join(codePath, document.fileName), code: document.getText() },
    };
}

const getRubyImportedCodeFiles = async (codePath: string, document: any) => {
    console.log("getRubyImportedCodeFiles called with codePath:", codePath);

    const payload = {};
    const imports = [];
    const importRegex = /^\s*require\s+['"](.+?)['"]/gm;
    let matches;

    const LocNo = document.lineCount > 50 ? 50 : document.lineCount;
    for (let i = 0; i < LocNo; i++) {
        const line = document.lineAt(i).text.replace(/\n/, "");
        if (line.includes("#")) continue;

        while ((matches = importRegex.exec(line)) !== null) {
            if (matches.length) {
                console.log("Import match found:", matches);
                imports.push({ file: matches[1] });
            }
        }
    }

    const finalImports = [];
    for (let row of imports) {
        let file = row.file;
        row.file = await getRubyFile(codePath, file); // Implement a `getRubyFile` function similar to `getPyFile`
        finalImports.push(row);
    }

    // Add content to payload
    payload.imports = finalImports;
    payload.main = { file: codePath, code: document.getText() };

    console.log("Payload for Ruby imports and main file:", payload);
    return payload;
};

export async function getRustImportedCodeFiles(codePath: string, document: vscode.TextDocument) {
    const payload = {};
    const imports = [];
    const importRegex = /^\s*use\s+((\w+::)*\w+);/gm;

    const codeText = document.getText();
    let matches;

    while ((matches = importRegex.exec(codeText)) !== null) {
        const importedModule = matches[1];
        imports.push(importedModule);
    }

    const finalImports = [];
    for (let importedFile of imports) {
        const filePath = await getRustFile(codePath, importedFile);
        if (filePath) {
            const fileData = await readFile(filePath);
            finalImports.push({ file: filePath, code: fileData });
        }
    }

    payload.imports = finalImports;
    payload.main = { file: codePath, code: codeText };

    return payload;
}

