//     // Start traversing the root level of the code tree
//     traverseSymbols(codeTree);

    
//     return detectedCode;
// }
export function parseCode(codeTree: vscode.DocumentSymbol[], document: vscode.TextDocument): { name: string; kind: vscode.SymbolKind; range: vscode.Range }[] {
    const detectedCode: { name: string; kind: vscode.SymbolKind; range: vscode.Range }[] = [];

    function traverseSymbols(symbols: vscode.DocumentSymbol[]) {
        if (!Array.isArray(symbols)) {
            // console.error("Expected 'symbols' to be an array but got", symbols);
            return; // Exit the function early if symbols is not an array
        }
        for (const symbol of symbols) {
            // Include classes, methods, functions, and variables
            if ([vscode.SymbolKind.Class, vscode.SymbolKind.Method, vscode.SymbolKind.Function, vscode.SymbolKind.Interface].includes(symbol.kind)) {
                detectedCode.push({
                    name: symbol.name,
                    kind: symbol.kind,
                    range: symbol.range,
                });
            }

            // Recursively check inner symbols
            if (symbol.children && symbol.children.length > 0) {
                traverseSymbols(symbol.children);
            }
        }
    }

    // Traverse the document symbols
    // traverseSymbols(codeTree);
    if (Array.isArray(codeTree)) {
        traverseSymbols(codeTree);
    }


    // Additional logic to detect React functional components
    const text = document.getText();
    const reactComponentRegex = /(?:export\s+default\s+)?(?:const|let|var|function)\s+([A-Z][a-zA-Z0-9]*)\s*=\s*(?:\(.*\)|function\s*\(.*\))/g;
    let match;
    while ((match = reactComponentRegex.exec(text)) !== null) {
        const componentName = match[1];
        const range = document.lineAt(document.positionAt(match.index).line).range;
        detectedCode.push({
            name: componentName,
            kind: vscode.SymbolKind.Function, // Treat as a function
            range: range,
        });
    }

    return detectedCode;
}


export function getLanguageType(language: string) {
    if (["typescriptreact", "typescriptjsx"].includes(language)) {
        language = "typescript";
    } else if (["javascriptreact", "javascriptjsx"].includes(language)) {
        language = "javascript";
    } else if (["java"].includes(language)) {
        language = "java";
    } else if (["cpp", "c++"].includes(language)) {
        language = "cpp";
    } else if (["csharp", "cs"].includes(language)) {
        language = "csharp";
    } else if (["php"].includes(language)) {
        language = "php";
    } else if (["ruby"].includes(language)) {
        language = "ruby";
    } else if (["rust"].includes(language)) {
        return "rust";
    }
    return language;
}
