export async function getTestableCodes(document: any) {
    const activeEditor = vscode.window.activeTextEditor;
    if (!activeEditor) return [];

    let language = getLanguageType(activeEditor.document.languageId);
    let parser;
    let codeTree;

    try {
        codeTree = await vscode.commands.executeCommand("vscode.executeDocumentSymbolProvider", activeEditor.document.uri);

        // Ensure codeTree is an array (or iterable) before passing it to parseCode
        if (!Array.isArray(codeTree)) {
            // console.error("Expected 'codeTree' to be an array but got", codeTree);
            return { detectedCode: [], importedCode: {} };
        }

        // Select appropriate parser based on language
        if (language === "php") {
            parser = new PhpParser();
        } else if (language === "python") {
            parser = new PyParser();
        } else if (["javascript", "typescript"].includes(language)) {
            parser = new JSParser();
        } else if (language === "java") {
            parser = new JavaParser();
        } else if (language === "go") {
            parser = new GoParser();
        } else if (language === "cpp") {
            parser = new CppParser();
        } else if (language === "csharp") {
            parser = new CsharpParser();
        } else if (language === "ruby") {
            parser = new RubyParser();
        } else if (language === "rust") {
            parser = new RustParser();
        } else {
            parser = new PyParser(); // Default parser
        }

        // Call parseCode with the validated codeTree
        const detectedCode = parseCode(codeTree, document);
        // console.log("Detected Code:", detectedCode);
        // console.log("Parser:", parser);

        return { detectedCode, importedCode: {} };
    } catch (error) {
        console.error("Error in getTestableCodes:", error);
        return { detectedCode: [], importedCode: {} };
    }
}
