export const getImportedContent = async (language: string, document: any, codePath: string) => {
    switch (language) {
        case 'php':
            return await getPhpImportedCodeFiles(codePath, document);
        case 'python':
            return await getPythonImportedCodeFiles(codePath, document);
        case 'ruby':
            return await getRubyImportedCodeFiles(codePath, document);
        case 'javascript':
        case 'typescript':
        case 'javascriptreact':
        case 'typescriptreact':
            return await getJSImportedCodeFiles(codePath, document);
        case 'csharp':
            return await getCsharpImportedCodeFiles(codePath, document);
        case 'cpp':
            return await getCppImportedCodeFiles(codePath, document);
        case 'rust':
            return await getRustImportedCodeFiles(codePath, document);
        default:
            return await getCsharpImportedCodeFiles(codePath, document); // Default case
    }
};