const fileContentsForNativeScript = [

    {
        filename : '.gitignore',
        content: `# NativeScript
hooks/
node_modules/
platforms/

# Logs
logs
*.log
npm-debug.log*
yarn-debug.log*
yarn-error.log*

# General
.DS_Store
.AppleDouble
.LSOverride
.idea
.cloud
.project
tmp/
typings/

# Visual Studio Code
.vscode/*
!.vscode/settings.json
!.vscode/tasks.json
!.vscode/launch.json
!.vscode/extensions.json
`,
    },
    {
        filename : 'nativescript.config.ts',
        content: `import { NativeScriptConfig } from '@nativescript/core';

export default {
  id: 'org.nativescript.app',
  appPath: 'app',
  appResourcesPath: 'App_Resources',
  android: {
    v8Flags: '--expose_gc',
    markingMode: 'none'
  }
} as NativeScriptConfig;
`,
    },
    {
        filename : 'package.json',
        content: `{
  "name": "samplejs",
  "main": "app/app.js",
  "version": "1.0.0",
  "private": true,
  "scripts": {
    "start": "setup-nativescript-stackblitz && ns preview"
  },
  "dependencies": {
    "@nativescript/core": "~8.8.0"
  },
  "devDependencies": {
    "@nativescript/preview-cli": "^1.0.14",
    "@nativescript/stackblitz": "^0.0.8",
    "@nativescript/tailwind": "^2.1.0",
    "@nativescript/webpack": "~5.0.0",
    "tailwindcss": "~3.4.0"
  }
}
`,
    },
    {
        filename : 'tailwind.config.js',
        content: `/** @type {import('tailwindcss').Config} */
module.exports = {
  content: [
    './app/**/*.{css,xml,html,vue,svelte,ts,tsx}'
  ],
  // use the .ns-dark class to control dark mode (applied by NativeScript) - since 'media' (default) is not supported.
  darkMode: ['class', '.ns-dark'],
  theme: {
    extend: {},
  },
  plugins: [],
  corePlugins: {
    preflight: false // disables browser-specific resets
  }
}
`,
    },
    {
        filename : 'webpack.config.js',
        content: `const webpack = require('@nativescript/webpack');

module.exports = (env) => {
  webpack.init(env);

  // Learn how to customize:
  // https://docs.nativescript.org/webpack

  return webpack.resolveConfig();
};
`,
    },

];

export const fileContentsForNativeScriptProject = [

    {
        filename : '.gitignore',
        content: `# NativeScript
hooks/
node_modules/
platforms/

# Logs
logs
*.log
npm-debug.log*
yarn-debug.log*
yarn-error.log*

# General
.DS_Store
.AppleDouble
.LSOverride
.idea
.cloud
.project
tmp/
typings/

# Visual Studio Code
.vscode/*
!.vscode/settings.json
!.vscode/tasks.json
!.vscode/launch.json
!.vscode/extensions.json
`,
    },
    {
        filename : 'nativescript.config.ts',
        content: `import { NativeScriptConfig } from '@nativescript/core';

export default {
  id: 'org.nativescript.app',
  appPath: 'app',
  appResourcesPath: 'App_Resources',
  android: {
    v8Flags: '--expose_gc',
    markingMode: 'none'
  }
} as NativeScriptConfig;
`,
    },
    {
        filename : 'package.json',
        content: `{
  "name": "samplejs",
  "main": "app/app.js",
  "version": "1.0.0",
  "private": true,
  "scripts": {
    "start": "setup-nativescript-stackblitz && ns preview"
  },
  "dependencies": {
    "@nativescript/core": "~8.8.0"
  },
  "devDependencies": {
    "@nativescript/preview-cli": "^1.0.14",
    "@nativescript/stackblitz": "^0.0.8",
    "@nativescript/tailwind": "^2.1.0",
    "@nativescript/webpack": "~5.0.0",
    "tailwindcss": "~3.4.0"
  }
}
`,
    },
    {
        filename : 'tailwind.config.js',
        content: `/** @type {import('tailwindcss').Config} */
module.exports = {
  content: [
    './app/**/*.{css,xml,html,vue,svelte,ts,tsx}'
  ],
  // use the .ns-dark class to control dark mode (applied by NativeScript) - since 'media' (default) is not supported.
  darkMode: ['class', '.ns-dark'],
  theme: {
    extend: {},
  },
  plugins: [],
  corePlugins: {
    preflight: false // disables browser-specific resets
  }
}
`,
    },
    {
        filename : 'webpack.config.js',
        content: `const webpack = require('@nativescript/webpack');

module.exports = (env) => {
  webpack.init(env);

  // Learn how to customize:
  // https://docs.nativescript.org/webpack

  return webpack.resolveConfig();
};
`,
    },
  {
    filename: "app",
    contents: [
      {
        filename: "app-root.xml",
        content:`<Frame defaultPage="main-page">
</Frame>
`
      },
      {
        filename: "app.css",
        content: `@tailwind base;
@tailwind components;
@tailwind utilities;
`
      },
      {
        filename: "app.js",
        content: `import { Application } from '@nativescript/core';

Application.run({ moduleName: 'app-root' });
`
      },
      {
        filename: "main-page.js",
        content: `import { createViewModel } from './main-view-model';

export function onNavigatingTo(args) {
  const page = args.object;
  page.bindingContext = createViewModel();
}
`
      },
      {
        filename: "main-page.xml",
        content: `<Page xmlns="http://schemas.nativescript.org/tns.xsd" navigatingTo="onNavigatingTo">
    <ActionBar title="My App" icon="" />
    <StackLayout class="p-4">
        <Label text="Please tap me" class="text-3xl text-center" />
        <Button text="TAP" tap="{{ onTap }}" class="-primary" />
        <Label text="{{ message }}" class="text-lg text-center text-gray-400" textWrap="true" />
    </StackLayout>
</Page>
`
      },
      {
        filename: "main-view-model.js",
        content: `import { Observable } from '@nativescript/core';

function getMessage(counter) {
  if (counter <= 0) {
    return 'Hoorraaay! You unlocked the NativeScript clicker achievement!';
  } else {
    return \`\${counter} taps left\`;
  }
}

export function createViewModel() {
  const viewModel = new Observable();
  viewModel.counter = 42;
  viewModel.message = getMessage(viewModel.counter);

  viewModel.onTap = () => {
    viewModel.counter--;
    viewModel.set('message', getMessage(viewModel.counter));

    // log the message to the console
    console.log(getMessage(viewModel.counter));
  };

  return viewModel;
}
`
      },
    ]
    }
];

export default fileContentsForNativeScript;