import axios from "axios";
//@ts-ignore
import { v4 as uuidv4 } from "uuid";
import { BASE_URL, AUTH_TOKEN, CMD_SIGNIN } from "./Constants";
import {StorageService} from './StorageService'
//@ts-ignore
import * as vscode from 'vscode';

export function getUniqueId() {
    return uuidv4();
}
// Parse jwt token
//@ts-ignore
export function parseJwt(token) {
    try {
        const base64Url = token.split(".")[1];
        const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
        return JSON.parse(atob(base64));
    } catch (e) {
        return {};
    }
}

export function getDate() {
    const curDate = new Date();
    return `${curDate.getFullYear()}-${curDate.getMonth() + 1}-${curDate.getDate()}`;
}

export async function authenticateUser(payload: any) {
    try {
        let url = `${BASE_URL}/v1/user/authenticate`
        console.log(`Authenticating user with payload ${payload}`)
        const headers = { "Content-Type": "application/json" };
        const response = await axios.post(url, JSON.stringify(payload), {
            headers,
        });
        console.log(`Authentication response: ${response}`)
        return response?.data || {};
    } catch (error) {
        console.error("Error authenticating user:", error);
        return {}
    }
}

//@ts-ignore
export async function doSignup(userPayload) {
    try {
        const headers = { "Content-Type": "application/json" };
        const response = await axios.post(`${BASE_URL}/v1/auth/uuid/signup`, JSON.stringify(userPayload), {
            headers,
        });
        return response;
    } catch (error) {
        console.error(error);
    }
}
//@ts-ignore
export async function getUserUsage(token, storage) {
    try {
        const headers = { Authorization: token };
        const response = await axios.get(`${BASE_URL}/v1/users/usage`, { headers: headers });
        const usage = { user_limit: response.data.user_limit, token_usage: response.data.token_usage };
        const remainingTokens = usage.token_usage > usage.user_limit ? 0 : usage.user_limit - usage.token_usage;
        if (storage) storage.setValue("usage", remainingTokens);
        return remainingTokens;
        // if (response.data.token_usage >= response.data.user_limit) return true;
        //@ts-ignore
    } catch (error) {
        console.error(error);
    }
}

export async function getIPAddress() {
    try {
      const response = await axios.get("https://api.ipify.org?format=json");
      console.log("RESPOSNE FROM Function", response.data)
      const data =  response.data;
      return data.ip;
    } catch (error) {
      console.error("Failed to fetch IP address:", error);
      return null; // Fallback if fetching the IP fails
    }
  }

export async function getUserProfile(token: string) {
    try {
        console.log("1402 Getting profile for", token);
        const headers = { Authorization: token };
        const response = await axios.get(`${BASE_URL}/v1/users/profile`, { headers: headers });        
        // If token is invalid. Delete token and Ask for login.
        console.log("1402 user profile", response);     
        // Clear token and show msg
        return response.data && response.data.user;
        //@ts-ignore
    } catch (error: any) {
        if ( error){
            // vscode.window.showErrorMessage("Token Expired/Invalid. Try Login");
            console.log(
                "Getting profile  Error while getting profile",
                error
            );
            console.log("Error code", error?.response?.status);
            if (error?.response?.status === 401) {
                const localConfig = vscode.workspace.getConfiguration();
                const localStore = new StorageService(localConfig);
                console.log("Getting profile for response status",);
                localStore.clear(AUTH_TOKEN);
                vscode.commands.executeCommand(CMD_SIGNIN, true);
            }

            
        }
        console.error("Error while getting profile", error);
        return "unauthorized";
    }
}


export async function askTerminalQuestion(userQuery: any, token: string) {
    try {
        console.log("Getting profile for", token);

        const headers = {
            Authorization: token,
            "Content-Type": "application/json",
        };

        const userPayload = {
            message: userQuery,
        };

        const response = await axios.post(`${BASE_URL}/api/v1/terminal/chat`, userPayload, {
            headers: headers,
        });

        console.log("Response askTerminalQuestion status:", response.status);

        if (response.status === 200) {
            const data = response.data; 
            console.log("Response askTerminalQuestion data:", data);

            return data || "not found";
        } else {
            console.error("Request failed with status:", response.status);
            return "not found";
        }
    } catch (error) {
        console.error("Error while getting profile:", error);
        return "not found";
    }
}


export async function getAccountUsage(token: string) {
    try {
        const headers = { Authorization: token };
        const response = await axios.get(`${BASE_URL}/v1/users/usage`, { headers: headers });
        console.log("user account usage response", response.data)
        return response.data;
        // if (response.data.token_usage >= response.data.user_limit) return true;
        //@ts-ignore
    } catch (error) {
        console.error(error);
    }
}

export async function updateAccountUsage(authToken: string, updatePayload: object) {
    try {
        console.log("Updating usage for", authToken, "updatePayload", updatePayload)
        const headers = {Authorization: `Bearer ${authToken}`, "Content-Type": "application/json"};
        const payload = JSON.stringify({
            "update_type": "account_usage",
            "update_payload": updatePayload
        });    
        const response = await axios.put(`${BASE_URL}/v1/users/account`, payload, {headers: headers});
        console.log("Update account usage response", response)
    }
    catch(error) {
        console.error("Error while updating usage", error);
    }


}


export async function updateUserProfile(token: string, profile: object) {
    try {
        console.log("Updating profile for", token, "profile", profile);
        const headers = { "Content-Type": "application/json" , Authorization: token};
        const response = await axios.patch(`${BASE_URL}/v1/users/profile`, JSON.stringify(profile), { headers: headers });
        // If token is invalid. Delete token and Ask for login.
        // Clear token and show msg
        return response.data && response.data.user;
        //@ts-ignore
    } catch (error) {
        console.error("Error while updating profile", error);
    }
}

export function getNonce() {
    let text = "";
    const possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (let i = 0; i < 32; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text;
}
