export class ComponentsUnderTestDataProvider {
    private _onDidChangeTreeData: vscode.EventEmitter<any>;
    public onDidChangeTreeData: vscode.Event<any>;
    private welcomeMessage: string;
    private userInfo: any = null; // Store user info

    constructor() {
        this._onDidChangeTreeData = new vscode.EventEmitter();
        this.onDidChangeTreeData = this._onDidChangeTreeData.event;
        this.welcomeMessage = "Select the file to get started...";

        this.listenToActiveTextEditorChanges();
        this.listenToDocumentChanges();
        this.fetchUserAccountDetails(); // Fetch user details on initialization
    }

    private listenToActiveTextEditorChanges() {
        vscode.window.onDidChangeActiveTextEditor(() => {
            this.refresh();
        });
    }

    private listenToDocumentChanges() {
        vscode.workspace.onDidChangeTextDocument(() => {
            this.refresh();
        });
    }

    private async fetchUserAccountDetails() {
        const authToken = getStoreKey(AUTH_TOKEN);
        if (!authToken) return;

        try {
            const authUser = await getUserProfile(authToken);
            if (!authUser?.email) return;

            // Fetch user plan details
            const response = await fetch(`${BASE_URL}/v1/users/account?email=${authUser.email}`);
            const data = await response.json();

            if (data) {
                const { plan_name, user_name, account_usage } = data;
                this.userInfo = data;

                this.welcomeMessage = `Welcome to GoCodeo, ${user_name || "User"}.
                You are currently on the ${plan_name || "Hobby"} plan.
                Premium model requests: ${account_usage.premium.used_requests}/${account_usage.premium.total_requests}.`;
                this.refresh();
            } else {
                this.welcomeMessage = "Failed to load account details.";
            }
        } catch (error) {
            console.error("Error fetching user account details:", error);
            this.welcomeMessage = "Error fetching account details.";
        }

        this.refresh(); // Refresh the sidebar after fetching user info
    }

    public refresh() {
        this._onDidChangeTreeData.fire(undefined);
        console.log("Refreshing sidebar");
    }

    public getTreeItem(element: vscode.TreeItem): vscode.TreeItem {
        return element;
    }

    public async getChildren(element: any): Promise<vscode.TreeItem[]> {
        const parentItems: vscode.TreeItem[] = [];

        // Display welcome message after sign-in instead of the code tree
        if (this.userInfo) {
            const { plan_name, user_name, account_usage } = this.userInfo;
            const welcomeLines = [
                `Welcome to GoCodeo, ${user_name || "User"}.`,
                `You are currently on the ${plan_name || "Hobby"} plan.`,
                `Premium model requests: ${account_usage.premium.used_requests}/${account_usage.premium.total_requests}.`
            ];

            // Create a separate TreeItem for each line
            welcomeLines.forEach((line) => {
                const item = new vscode.TreeItem(line, vscode.TreeItemCollapsibleState.None);
                item.iconPath = new vscode.ThemeIcon("debug-breakpoint");
                parentItems.push(item);
            });

            return Promise.resolve(parentItems);
        }

        // If user info is not available, return an empty tree
        return Promise.resolve([]);
    }

}
